.missionContainer {
    display: flex;
    margin: 1rem;
    .fixedLeft {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        .leftSideButton{
            line-height: normal;
            background-color: #F0C059;
            color: #874404;
            font-weight: 600;
            width: 10rem;
        }
    }
    .scrollRight {
        overflow: auto;
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
        margin: 0 1rem;
        padding: 0 1rem 0 0;
        height: 600px;
        .banner {
            display: flex;
            align-items: center;
            background-color: #1C1E23;
            padding: 1rem;
            border-radius: 7px;
            gap: 5px;
            .phoneImageBox {
                width: 10%;
                display: flex;
                align-items: center;
                justify-content: center;
                .phoneImage {
                    width: 3.5rem;
                }
            }
            .labelBox {
                display: flex;
                flex-direction: column;
                gap: 10px;
                width: 100%;
                .boxLabel {
                    font-size: 20px;
                    color: #fff;
                    .labelContent{
                        color: #FFAA09;
                    }
                }
                .bonusLabel{
                    color: #68707B;
                }
            }
            .buttonBox{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 18%;
                .downloadButton{
                    background-color: #F0C059;
                    color: #874404;
                    font-weight: 600;
                }
            }
        }
        .contextBox{
            background-color: #1C1E23;
            padding: 1rem;
            border-radius: 7px;
            color: #fff;
            font-size: 18px;
            display: flex;
            flex-direction: column;
            gap: 10px;
            .missionLabels{
                display: flex;
                flex-direction: column;
                gap: 10px;
            }
        }
    }
}

.css-rnmm7m-MuiPaper-root-MuiDialog-paper{
    background: #202329 !important;
    border-radius: 7px !important;
}