.pointRedemptionMainContainer {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .pointRedemptionContainer {
        display: flex;
        flex-direction: column;
        margin-bottom: 1.5rem;
        background-color: #1C1E23;
        border-radius: 7px;
        padding: 1rem;
        gap: 1.5rem;
        .dateButton {
            background-color: #f0c059;
            color: #874404;
            font-weight: 600;
            line-height: normal;
        }

        .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
            border-color: #f0c0599e !important;
        }

        .Mui-focused {
            .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
                border-color: #f0c059 !important;
            }
        }
        .Mui-disabled{
            .css-1n4twyu-MuiInputBase-input-MuiOutlinedInput-input{
                -webkit-text-fill-color: #f0c0599e;
            }
