}
.pointExContentBox{
    .pointExContent{
        display: flex;
        flex-direction: column;
        gap: 15px;
        .pointRedemptionRatioBox{
            display: flex;
            gap: 0 !important;
            .pointLabel{
                margin-right: 5px;
            }
        }
        .pointContentBox{
            display: flex;
            gap: 5px;
            .pointLabel{
                color: #fff;
            }
            .pointCurrency{
                color: #f0c059;
                font-style: italic;
            }
            .pointContent{
                font-weight: 600;
                color: #fff;
            }
            .pointSelectBox{
                .css-u4tvz2-MuiFormLabel-root{
                    color: #fff;
                }
                .css-u4tvz2-MuiFormLabel-root.Mui-focused,
                .css-1de7op1-MuiButtonBase-root-MuiRadio-root.Mui-checked{
