                }
            }
            .ratioDivider,
            .pointRatio2,
            .pointRatio1{
                color: #f0c059;
                font-weight: 600;
                font-style: italic;
            }
            .pointLabelMax,
            .pointLabelMin{
                color: #f0c059;
                font-style: italic;
            }
            .pointLabelMaxContent,
            .pointLabelMinContent{
                font-weight: 600;
            }
            .pointLabelMinContent,
            .pointLabelMaxContent{
                color: #fff;
                font-style: italic;
            }
            .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
                border-color: #f0c0599e !important;
            }
    
            .Mui-focused {
                .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
                    border-color: #f0c059 !important;
                }
            }
            .Mui-disabled{
