.login-dialog-dialog{
   .modalTitle{
    background-color: #1c1e23;
    color: white;
    text-align: center;
    
   }
   .MuiPaper-root{
    background-color: #1c1e23;
    border-radius: 20px;
    .modalContent{
        display: flex;
        flex-direction: column;
        gap: 15px;
        .inputField{
            display: flex;
          
            align-items: center;
            border:.25px solid #313843;
            padding: 5px 8px;
            border-radius: 5px;
            &:has(input:focus){
                border-color: #F0C059;
            }
            .icon{
                svg{
                    height: 1.8rem;
                    width:1.8rem;
                    color:#7f7f7f;
                }
            
            }
            input{
                background: transparent;
                border: none;
                outline: none;
                width: 20vw;
                height: 100%;
                font-size: 18px;
                padding: 10px;
                color: white;
                
            }
        }
        .passwordControlls{
            display: flex;
            justify-content: space-between;
            color:#7f7f7f;
            align-items: center;
            padding: 0 5px;
            .forgotPassword{
                a{
                    text-decoration: none;
                    font-size: 18px;
                    font-weight: 600;
                    color: #F0C059;
                }
            }
        }
    }
 
    .buttonContainer{
        padding: 20px 20px;
        .loginButton{
            background-color: #F0C059;
            border-color: #F0C059;
            width: 100%;
            color:#874404;
            padding: 10px;
        }
    }
    .extraAction{
        display: flex;
        padding:1rem 3rem 2rem 3.5rem;
        justify-content: space-between;
        a{
            text-decoration: none;
            color: #F0C059;
            font-size: 22px;
        }
    }
   }
}
.foo{
   
    .Toastify__progress-bar {
        background-color: #F0C059;
    }
}