.slider-container img {
  height: 20vh;
  border-radius: 10px;
  width: 20vw;
  border: 2px solid rgb(65, 63, 63);
}
.slider-container ul li button::before {
  color: white;
  border-radius: 10px;
  margin: 0;
  padding: 0;
}
.slider-container ul li button.slick-active {
  color: red;
}

.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  display: flex !important;
  justify-content: space-evenly !important;
}

.swiper-slide img {
  display: block;
  width: 40vw;
  height: 30vh;
}

:root {
  --swiper-theme-color: white !important;
}

.swiper-slide {
  flex-shrink: 1 !important;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
  display: block;
}/*# sourceMappingURL=carousel.css.map */