.vipMainContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .vipHeader{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        // margin-bottom: 1.5rem;
        gap: 10px;
        border-radius: 7px;
        .titleWing{
            width: 5rem;
            &:nth-child(3){
                rotate: 180deg;
            }
        }
        .vipHeaderTitle{
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }
    .medalMainContainer{
        background-color: #1C1E23;
        display: flex;
        padding: 1rem;
        border-radius: 7px;
        .medalBox{
            display: flex;
            align-items: center;
            width: 100%;
            .medalContainer{
                display: flex;
                justify-content: center;
                align-items: center;
                // background-image: url("/public/vipImages/medal.png");
                background-repeat: no-repeat;
                background-size: contain;
                position: relative;
                width: 100px;
                height: 100px;
                .medalRibbonContainer{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    // background-image: url("/public/vipImages/medalRibbon.png");
                    background-repeat: no-repeat;
                    background-size: contain;
                    position: relative;
                    width: 100px;
                    height: 100px;
                    .vipLevel{
                        color: #e4b74f;
                        font-size: 40px;
                        position: relative;
                        top: 0px;
                        font-weight: 600;
                        text-shadow: 1px 1px 2px #161616;
                        font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, "Segoe UI", Arial, Roboto, "PingFang SC", miui, "Hiragino Sans GB", "Microsoft Yahei", sans-serif;;
                    }
                }
            }
            .medalLabel{
                font-size: 22px;
                color: #949494;
                font-weight: 400;
                &:nth-child(2){
                    margin-left: 1rem;
                }
            }
            .medalContent{
                font-size: 22px;
                &:nth-child(3){
                    font-weight: 600;
                    font-style: italic;
                    color: red;
                }
                &:nth-child(5){
                    font-weight: 600;
                    color: #fff;
                }
            }
        }
        .medalButtonContainer{
            width: 30%;
            display: flex;
            justify-content: center;
            align-items: center;
            .vipButtons{
                line-height: normal;
                background-color: #F0C059;
                color: #874404;
                font-weight: 600;
            }
        }
    }
    .vipLevelMainContainer{
        margin-bottom: 1.5rem;
        background-color: #1C1E23;
        border-radius: 7px;
        .levelHeader{
            padding: 1rem;
            label{
                font-size: 24px;
                font-weight: 500;
                color: #fff;
            }
        }
        .divLine{
            border: thin solid #313843;
        }
        .vipTabs{
            color: #fff;
            font-weight: 600;
        }
        .MuiTabs-root{
            button{
                color:white;
            }
            button[aria-selected="true"]{
                color: #f0c059;
            }
        }
        .vipLevelTableContainer{
            padding: 1rem;
            .vipTableHead{
                height: 5.5rem;
                background-color: #202329;
                .tableHeader{
                    font-size: 23px;
                    color: #fff;
                }
            }
            .borderedRow{
                &.active{
                    border: 2px solid #e4b74f;
                }
            }
            .vipTableData{
                color: #9d9d9d;
                font-size: 18px;
                .secondData{
                    background-color: #919191;
                    color: #fff;
                    // width: 20rem;
                    margin: auto;
                    line-height: normal;
                    border-radius: 10px;
                    font-size: 16px;
                }
            }
            .coloredData{
                color: #F0C059;
            }
            .medalRowContainer{
                display: flex;
                align-items: center;
                justify-content: center;
                .medalContainer{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    // background-image: url("/public/vipImages/medal.png");
                    background-repeat: no-repeat;
                    background-size: contain;
                    position: relative;
                    width: 75px;
                    height: 75px;
                    .medalRibbonContainer{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        // background-image: url("/public/vipImages/medalRibbon.png");
                        background-repeat: no-repeat;
                        background-size: contain;
                        position: relative;
                        width: 75px;
                        height: 75px;
                        .vipLevel{
                            color: #e4b74f;
                            font-size: 30px;
                            position: relative;
                            font-weight: 600;
                            text-shadow: 1px 1px 2px #161616;
                            font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, "Segoe UI", Arial, Roboto, "PingFang SC", miui, "Hiragino Sans GB", "Microsoft Yahei", sans-serif;;
                        }
                    }
                }
            }
        }
    }
    .instructionMainContainer{
        width: 100%;
        margin-bottom: 1.5rem;
        background-color: #1C1E23;
        border-radius: 7px;
        .titleContainer{
            padding: 1rem;
            label{
                color: #fff;
                font-weight: 500;
                font-size: 24px;
            }
        }
        .divLine{
            border: thin solid #313843;
        }
        .instructionContainer{
            padding: 1rem;
            color: #ADB6C3;
            display: flex;
            flex-direction: column;
            gap: 10px;
            font-size: 17px;
        }
    }
}