.mainLayout {
    border: 1px solid black;
    height: 100vh;
    position: relative;

    .mainContent {
        height: 91vh;
        display: flex;

        .centerContent {
            height: 100%;
            background-image: url('https://mbp399-5615-ppp.s3.sa-east-1.amazonaws.com/siteadmin/skin/lobby_asset/2-1-7/common/common/bg_pattern_tile.png?manualVersion=1&version=049cb844f0');
            overflow: auto;
            width: 100%;
            background-color: var(--bodyColor);
            background-color: #202329;

            .contentContainer {
                width: 100%;
                height: auto;
                padding-top: 2rem;
                min-height: 1072px;
                position: relative;
                .envelopeContainer {
                    position: fixed;
                    right: 54px;
                    bottom: 20vh;
                    background: linear-gradient(180deg, #ed812d, #feba5a);
                    border-radius: 55px;
                    cursor: pointer;
                    height: 3.5rem;
                    width: 3.5rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    top: 43rem;
                    img {
                        height: 50px;
                    }
                   }
                section {
                    width: 66vw;
                    margin: auto;
                    height: 100%;
                }
                .scrolltoTop{
                    position: fixed;
                   
                    height: 3rem;
                    width: 3rem;
                    border-radius: 50%;
                    bottom: 35px;
                    right: 3.8rem;
                   
                    cursor: pointer;
             
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .gift-container {
                    background: linear-gradient(180deg, #ed812d, #feba5a);
                    cursor: pointer;
                    border-radius: 50%;
                    position: fixed;
                    right: 55px;
                    bottom: 100px;
                    height: 3.5rem;
                    width: 3.5rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img {
                        position: relative;
                        height: 40px;
                        
                    }
                }
        
            }
        }
    }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

