.navbarContainer {
    border-bottom: 3px solid #313843;
    width: 100%;
    height: 9vh;
    .navbar {
        display: flex;
        background-color: #141417;
        width: 100%;
        height: 100%;

        .navLeft {
            width: 65%;
            display: flex;
            align-items: center;

            .navArrow {
                color: #ebe7e7;
                width: calc(2rem);
                height: calc(2rem);
                margin-left: 1rem;
                margin-right: 1.5rem;
                cursor: pointer;
                transition: rotate 200ms ease;
                &.active{
                    rotate: 180deg;
                    transition: rotate 200ms ease;
                }
            }

            .logoContainer {
                cursor: pointer;

                .logoImage {
                    width: calc(10rem);
                }
            }

            .timeContainer {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                color: #d6d6d6;
                font-size: 13px;
                margin-left: 3rem;
            }
        }

        .navMiddle {
            display: flex;
            align-items: center;
            width: 100%;
            gap: 15px;

            .moneyDisplayBox {
                border: thin solid #313843;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 10px;

                .moneyFlag {
                    width: 2.2rem;
                    height: 2.2rem;
                    color: green;
                }

                .moneyValue {
                    color: #FFAA09;
                    font-size: 24px;
                }

                .refreshIcon {
                    width: 2.2rem;
                    height: 2.2rem;
                    color: #f0c059;
                    cursor: pointer;

                    &.active {
                        -webkit-animation: spin .5s linear infinite;
                        animation: spin .5s linear infinite;
                    }
                }
            }

            .buttonContainer {
                .navButtons {
                    background-color: transparent;
                    border: thin solid #f0c059;
                    color: #f0c059;
                    font-weight: 600;
                    min-width: 7rem;
                    max-width: max-content;

                    &.active {
                        background-color: #f0c059;
                        color: #874404;
                    }
                }
            }

            &.active {
                display: none;
            }
        }

        .navRight {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: end;

            .credentialButtons {
                display: flex;
                flex-direction: row-reverse;
                align-items: center;
                height: 100%;
                margin-right: 2rem;

                .credentialButton {
                    background-color: #f0c059;
                    color: #874404;
                    font-weight: 600;
                    min-width: 7rem;
                    max-width: 7.5rem;

                    &:nth-child(2) {
                        background-color: #141417;
                        border: 1px solid #f0c059;
                        color: #f0c059;
                    }
                }

                &.active {
                    display: none;
                }
            }

            .menuBox {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                width: fit-content;
                padding: 5px;
                margin-right: 2rem;
                border-radius: 7px;
                cursor: pointer;

                .css-137ul40-MuiButtonBase-root-MuiIconButton-root {
                    margin: 0;
                }

                .profileMenu {
                    display: flex;
                    gap: 10px;

                    .profileImage {
                        width: 3.5rem;
                    }

                    .UserInfo {
                        color: #ADB6C3;
                        display: flex;
                        flex-direction: column;
                        justify-content: start;
                        align-items: flex-start;
                        width: 5.8rem;
                        cursor: pointer;

                        .usernameLabel {
                            cursor: pointer;
                        }

                        .userCopyBox {
                            display: flex;
                            gap: 5px;

                            .copyIcon {
                                margin-left: .5rem;
                                width: 1.3rem;
                                height: 1.3rem;
                                color: #F0C059;
                            }
                        }
                    }

                    .arrowDownIcon {
                        width: 2rem;
                        height: 2rem;
                    }

                    .css-2gu0xr-MuiAvatar-root {
                        top: 4px;
                    }
                }

                &:hover {
                    background-color: #1f1f22;
                }

                &.active {
                    display: none;
                }
            }

            .colorPicker {
                margin-right: 2rem;

                // .Mui-focused .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
                //     border-color: red !important;
                // }

                // .css-1jy569b-MuiFormLabel-root-MuiInputLabel-root {
                //     color: red !important;
                // }

                // .css-14s5rfu-MuiFormLabel-root-MuiInputLabel-root,
                // .css-1t8l2tu-MuiInputBase-input-MuiOutlinedInput-input,
                // .css-1n4twyu-MuiInputBase-input-MuiOutlinedInput-input {
                //     color: red !important;
                // }

                // .css-1pysi21-MuiFormLabel-root-MuiInputLabel-root {
                //     color: red !important;
                // }


                // .css-1jy569b-MuiFormLabel-root-MuiInputLabel-root,
                // .css-jedpe8-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input,
                // .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon,
                // .css-bpeome-MuiSvgIcon-root-MuiSelect-icon,
                // .css-1n4twyu-MuiInputBase-input-MuiOutlinedInput-input,
                // .css-1pysi21-MuiFormLabel-root-MuiInputLabel-root {
                //     color: red !important;
                // }
                // .css-3dzjca-MuiPaper-root-MuiPopover-paper-MuiMenu-paper{
                //     background-color: red !important;
                // }

                // .css-eqeipb-MuiPaper-root-MuiPopover-paper-MuiMenu-paper::before {
                //     background-color: red !important;
