.depostSection {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    .secondSection {
        background-color: #1C1E23;
        border-radius: 7px;
        margin-bottom: 1.5rem !important;

        .supportTabs {
            color: #fff;
            font-weight: 600;
        }

        .MuiTabs-root {
            button {
                color: white;
            }

            button[aria-selected="true"] {
                color: #f0c059;
            }
        }

        .transferMainContainer {
            display: flex;
            flex-direction: column;
            padding: 1rem;
            gap: 1rem;

            .cardContainer {
                display: flex;
                gap: 1rem;

                .bankCards {
                    display: flex;
                    position: relative;
                    align-items: center;
                    justify-content: center;
                    text-align: center;
                    width: 6rem;
                    height: 4rem;
                    display: flex;
                    padding: 1rem;
                    background-color: #282b32;
                    border-radius: 7px;
                    border: 2px solid;
                    border-color: #f0c05955;
                    cursor: pointer;
                    font-weight: 600;
                    color: #f0c059;
                    letter-spacing: 2px;

                    &.active {
                        border-color: #f0c059;
                    }
                }

                .checkContainer {
                    overflow: hidden;
                    position: relative;
                    border-radius: 7px;

                    .badge {
                        display: none;
                        position: absolute;
                        right: 0px;
                        bottom: 0;
                        padding: 3px;
                        rotate: 45deg;
                        height: 50px;
                        width: 50px;
                        margin: -25px;

                        &.active {
                            background-color: #f0c059;
                            display: block;
                        }

                        .checkIcon {
                            color: #874404;
                            rotate: -45deg;
                            right: 10px;
                        }

                        .css-17o22dy-MuiAvatar-root {
                            top: 11px !important;
                            right: 4px !important;
                        }
                    }
                }
            }

            .paymentMethod {
                display: flex;
                flex-direction: column;

                .paymentMethodDetails {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 1rem;

                    .details {
                        background-color: #282b32;
                        padding: 1rem;
                        border-radius: 7px;
                        display: flex;
                        align-items: center;
                        gap: 10px;
                        width: 100%;

                        .detailsLabel {
                            color: #fff;
                            font-style: italic;
                        }

                        .detailsContent {
                            font-size: 19px;
                            font-weight: 600;
                        }
                    }

                    .crdBank {
                        display: flex;
                        flex-direction: column;
                        background-color: #282b32;
                        width: 22rem;
                        height: 10rem;
                        align-items: center;
                        justify-content: center;
                        border-radius: 7px;
                        border: 2px solid;
                        border-color: #f0c059;
                        padding: 1rem;

                        .cardName {
                            font-size: 22px;
                            font-weight: 600;
                        }

                        .cardBody {
                            .cardBox {
                                display: flex;
                                gap: 10px;

                                .cardLabel {
                                    font-style: italic;
                                    color: #fff;
                                }

                                .cardContent {
                                    font-weight: 600;
                                }
                            }
                        }
                    }
                }
            }

            .qrCodeContainer {
                display: flex;
                flex-direction: column;
                gap: 1rem;

                .qrCodeLabelBox {
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;

                    .details {
                        background-color: #282b32;
                        padding: 1rem;
                        border-radius: 7px;
                        display: flex;
                        align-items: center;
                        gap: 10px;
                        width: 100%;
                        color: #fff;
                        font-style: italic;
                    }

                    .qrCodeLabel {
                        font-size: 14px;
                        font-style: italic;
                        width: 40rem;
                    }
                }

                .qrCodeBox {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .qrCodeImage {
                        width: 17rem;
                        border-radius: 2rem;
                    }
                }

                .paymentChannel {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .payName {
                        display: flex;
                        flex-direction: column;
                        background-color: #282b32;
                        width: 15rem;
                        height: 5rem;
                        align-items: center;
                        justify-content: center;
                        border-radius: 7px;
                        border: 2px solid;
                        border-color: #f0c059;
                        padding: 1rem;
                        font-size: 20px;
                        text-transform: uppercase;
                        letter-spacing: 2px;
                        font-weight: 600;
                    }
                }

                .depoAmountContainer {
                    display: flex;
                    gap: 1rem;

                    .amountValue {
                        background-color: #282b32;
                        padding: 1rem;
                        border: 2px solid;
                        border-color: #f0c05955;
                        border-radius: 7px;
                        cursor: pointer;

                        &.active {
                            border-color: #f0c059;
                        }
                    }

                    .checkContainer {
                        overflow: hidden;
                        position: relative;
                        border-radius: 7px;

                        .badge {
                            position: absolute;
                            right: 0px;
                            bottom: 0;
                            padding: 3px;
                            display: none;
                            rotate: 45deg;
                            height: 50px;
                            width: 50px;
                            margin: -25px;

                            &.active {
                                background-color: #f0c059;
                                display: block;
                            }

                            .checkIcon {
                                color: #874404;
                                rotate: -45deg;
                                right: 10px;
                            }

                            .css-17o22dy-MuiAvatar-root {
                                top: 11px !important;
                                right: 4px !important;
                            }
                        }
                    }
                }

                .css-10drtbx-MuiButtonBase-root-MuiCheckbox-root:hover {
                    color: #f0c059;
                }

                .css-10drtbx-MuiButtonBase-root-MuiCheckbox-root {
                    color: #f0c059;
                }
            }

            .buttonContainer {
                .configButton {
                    background-color: #f0c059;
                    color: #874404;
                    font-weight: 600;
                }
            }

            .inputContainer {
                display: flex;
                flex-direction: column;
                gap: 1rem;

                .details {
                    background-color: #282b32;
                    padding: 1rem;
                    border-radius: 7px;
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    width: 100%;
                    color: #fff;
                    font-style: italic;
                }

                .buttonContainer {
                    .configButton {
                        background-color: #f0c059;
                        color: #874404;
                        font-weight: 600;
                    }
                }

                .css-10drtbx-MuiButtonBase-root-MuiCheckbox-root:hover {
                    color: #f0c059;
                }

                .css-10drtbx-MuiButtonBase-root-MuiCheckbox-root {
                    color: #f0c059;
                }
            }

            .notesContainer {
                display: flex;
                flex-direction: column;
                gap: 10px;

                .details {
                    background-color: #282b32;
                    padding: 1rem;
                    border-radius: 7px;
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    width: 100%;
                    color: #fff;
                    font-style: italic;
                }

                .minNote {
                    display: flex;
                    flex-direction: column;

                    .noteLabelBox {
                        display: flex;

                        .noteLabel {
                            color: #fff;
                            margin-right: 10px;
                            font-size: 16px;
                            font-style: italic;
                        }

                        .noteLabelContent {
                            margin-right: 3px;
                            font-size: 16px;
                            font-weight: 600;
                        }

                        .miniCurrency {
                            font-size: 16px;
                            font-weight: 600;
                            color: #874404;
                        }
                    }
                }
            }
        }
    }

}