.gameContainers-tab{
    display: flex;
    margin-top: 1rem;
    margin-bottom: 1rem;
    .gameSidebars{
        height: 100%;
        width: 15%;
        padding: 1rem;
        padding-top: 0;
        .tabContainer{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 1rem;
            .tabs{
                background-color:#1C1E23 ;
                width: fit-content;
                padding: .5rem;
                border-radius: 10px;
                border:2px solid  #9e9e9e ;
                cursor: pointer;
                overflow: hidden;
                position:relative;

                .icon{
                    width:5rem;
              
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-self: center;
                    gap: .5rem;
                    img{
                        max-width: 3.5rem;
                        width: 100%;
                        height: 100%;
                    }
                    span{
                        color: #9e9e9e;
                        font-size: 14px;
                        text-align: center;
                        text-transform: uppercase;
                    }
                }
                &:hover,
                &.active{
                    background-color:#f0c059 ;
              
                    border: 2px solid #f0c059;
                    span{
                     
                        color: #ffffff;
                    }
                   
                }
            }

        }
    }
    .mainGames{
        background-color: #1C1E23;
        width: 100%;
        border-radius: 10px;
        overflow: auto;
        max-height: 60rem;
        .gameContainer{
            padding: 1rem;
            .gameTab{
                padding: 1rem;
                display: flex;
                gap: 2rem;
                column-gap: 2.3rem;
                flex-wrap: wrap;
          .gameTabContainerSub{
            position: relative;
            width: 11rem;
            background-image: url('/public/images/loading-bg.png');
            border-radius: 25px;
            cursor: pointer;
            .playBtn{
                position: absolute;
                bottom: 0;
                top: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                width: 100%;
            .icon{
                width: 5rem !important;
                height: 5rem !important;
                opacity: 0;
                transition:opacity 400ms ease ;
                cursor: pointer;
            }
        }
            &:hover{
                .playBtn{
                    position: absolute;
                    bottom: 0;
                    top: 0;
                    background-color: #05050559;
                    border-radius:25px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .icon{
                        opacity: 1;
                        transition:opacity 400ms ease ;
                    }
                }
                
            }
            img{
                width: 11rem !important;
                height: 14rem !important;
    
             }
                             
             .game{
                width: 100%;
                height: 100%;
                border-radius: 25px;
                background-repeat: no-repeat;
                background-size: 100% 100%;
                cursor: pointer;
                position: relative;

            }
          }

            }
        }
    }
}
.frameContainer{
    .frameControlers{
        background-color: #2b2d33;
        display: flex;
        padding: 0.5rem;
        border-radius: 1rem;
        justify-content: space-between;
        .sizeClose{
            display: flex;
            align-items: center;
            gap: 2rem;
            .closeGame{
                display: flex;
                align-items: center;
                gap: .5rem;
                img{
                    height: 25%;
                    width: 25%;
                }
            }
            .fullScreen{
                display: flex;
                align-items: center;
                gap: .5rem;
                img{
                    height: 25%;
                    width: 25%;
                }
            }
            div{
                color: #f0c059;
                font-size: 20px;
                cursor: pointer;
            }
        }
        .frags{
            display: flex;
            align-items: center;
           gap: 1rem;
            span{
                color: #f0c059;
                font-size:  1.5rem;
                cursor: pointer;
                text-transform:uppercase
            }
            img{
                max-width: 2rem;
                rotate:270deg;
            }
            .tail{
                color: #9e9e9e;
                cursor: default;
            }
        }
    }
    .frameGrme{
        background-color: #2b2d33;
        margin-top: 1rem;
        margin-bottom: 1rem;
        border-radius: 1rem;
        height: 45rem;
        iframe{
            width: 100%;
            height: 100%;
            
        }
    }
}