.main-event {
    color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content:end;
    position: relative;
    font-size: 100px;
    img {
        height: 25vh;
        margin: 10px;
        width: 25vw;
        border-radius: 10px;
    }
    .button-link {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        left: 0;
        img {
            height: 70px;
            width: 150px;
            cursor: pointer;
        }
        #reg {
            height: 50px;
            width: 5vw;
            cursor: pointer;
        }
    }
  }

  
  