.promoRegistrationMainContainer{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .header{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        gap: 10px;
        border-radius: 7px;
        .titleWing{
            width: 5rem;
            &:nth-child(3){
                rotate: 180deg;
            }
        }
        .headerTitle{
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }
    .promoMainContainer{
        background-color: #1C1E23;
        display: flex;
        flex-direction: column;
        border-radius: 7px;
        padding: 1rem;
        gap: 1rem;
        .optionInputContainer{
            display: flex;
            flex-direction: column;
            gap: 1rem;
            .css-1yk1gt9-MuiInputBase-root-MuiOutlinedInput-root-MuiSelect-root.Mui-focused .MuiOutlinedInput-notchedOutline{
                border-color: #f0c059;
                border-width: 2px;
            }
            .MuiOutlinedInput-notchedOutline{
                border-color: #f0c0596b;
                border-width: 1px;
            }
            .css-1jy569b-MuiFormLabel-root-MuiInputLabel-root{
                color: #f0c059;
            }
            .css-1pysi21-MuiFormLabel-root-MuiInputLabel-root{
                color: #ffffff6b;
            }
            .css-jedpe8-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input.MuiSelect-select{
                color: #fff;
            }
            .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon, .css-bpeome-MuiSvgIcon-root-MuiSelect-icon{
                color: #874404;
            }
            .generateButton{
                background-color: #f0c059;
                color: #874404;
                font-weight: 600;
            }
        }
    }
    .tableContainer{
        .linkBox{
            max-width: 9rem;
            min-width: 8rem;
            word-wrap: break-word;
            .promoLink{
                color: #f0c059;
                font-style: italic;
                font-weight: 600;
            }
        }
        .promoCodeBox{
            width: 10rem;
        }
        .rebateBox{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .labelBox{
                display: flex;
                width: max-content;
                gap: 5px;
                .label{
                    font-style: italic;
                }
                .labelContent{
                    color: #f0c059;
                    font-weight: 600;
                }
            }
        }
        .deleteButton{
            background-color: #f0c059;
            color: #874404;
            font-weight: 600;
        }
        .css-1q1u3t4-MuiTableRow-root{
            th{
                font-weight: 600;
                background-color: #b3adad;
            }
        }
        // .css-11xur9t-MuiPaper-root-MuiTableContainer-root{
        //     background-color: #ece0e0;
        // }
        .css-1nfbgt6-MuiTable-root{
            background-color: rgba(158, 145, 145, 0.361)
        }
    }
}