.tableContainer {
    display: table;
    width: 100%;
    border-spacing: 0;
    border-collapse: separate;
    overflow: auto;
    .tableHead {
        display: table-header-group;
        border-radius: 5px;
        background-color: #202329;
        font-size: 14.5px;

        .tableRow {
            display: table-row;

            &:first-child {
                .tableCell {
                    &:first-child {
                        border-top-left-radius: 7px;
                        border-bottom-left-radius: 7px;
                        border: 2px solid;
                        border-color: #f0c059a5;
                        border-right: none;
                    }

                    &:last-child {
                        border-top-right-radius: 7px;
                        border-bottom-right-radius: 7px;
                        border: 2px solid;
                        border-color: #f0c059a5;
                        border-left: none;
                    }

                    &:not(:first-child):not(:last-child) {
                        border: 2px solid;
                        border-color: #f0c059a5;
                        border-left: none;
                        border-right: none;
                    }
                }
            }

            .tableCell {
                display: table-cell;
                color: white;
                text-align: center;
                font-weight: bold;
                padding: 1.2rem;
                border-right: none;
                border-left: none;
                vertical-align: middle;
            }
        }
    }

    .tableBody {
        display: table-row-group;
        font-size: 13.5px;

        .tableRow {
            display: table-row;
            transition: all 0.3s ease;

            &:nth-child(even) {
                background-color: #202329;

                .tableCell {
                    &:first-child {
                        border-top-left-radius: 7px;
                        border-bottom-left-radius: 7px;
                        border: thin solid;
                        border-color: #f0c0596e;
                        border-right: none;
                    }

                    &:last-child {
                        border-top-right-radius: 7px;
                        border-bottom-right-radius: 7px;
                        border: thin solid;
                        border-color: #f0c0596e;
                        border-left: none;
                    }

                    &:not(:first-child):not(:last-child) {
                        border: thin solid;
                        border-color: #f0c0596e;
                        border-left: none;
                        border-right: none;
                    }
                }
            }

            .tableCell {
                display: table-cell;
                text-align: center;
                color: white;
                padding: .8rem;
                vertical-align: middle;
            }
        }
    }
}
