.promoContianer {
    display: flex;
    padding: 1rem;
    gap: 1rem;
    border-radius: 10px;

    .promoLinks {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        button {
            font-size: 17px;
            padding: 10px;
            background-color: #181b29;
            color: #fff;
            border: none;
            cursor: pointer;
            border-radius: 5px;
            width: 7rem;
        }
    }

    .promoMain {
        height: 100%;
        background-color: #181b29;
        width: 100%;
        padding: 1rem;
    }
}

.top1 {
    display: flex;
    justify-content: center;

    .CardContainer {
        display: flex;
        border: 1px solid #f0c059;
        width: 70%;
        padding: 1rem;
        border-radius: 10px;

        .card-agent {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            width: 40%;
            align-items: center;
            justify-content: center;

            .textArea {
                display: flex;
                flex-direction: column;
                gap: 5px;
                align-items: center;
                justify-content: center;

                .textTop {
                    color: #fff;
                    font-size: 18px;
                }

                .textBottom {
                    color: #f0c059;
                    font-size: 18px;
                }
            }

        }

        .card-right {
            width: 60%;
            display: flex;
            flex-direction: column;
            padding: .5rem;
            gap: .5rem;

            div {
                color: #fff;
                font-size: 18px;
            }

            em {
                color: #f0c059;
                font-size: 18px;
            }
        }
    }
}

.arrowMid {
    display: flex;
    width: 100%;
    gap: 2rem;
    padding: 1rem;
    height: 15rem;
    justify-content: center;

    .left {
        position: relative;
        rotate: -42deg;
        left: 6rem;
        top: 2rem;

        div {
            color: #fff;
            font-size: 18px;
        }

        em {
            color: #f0c059;
            font-size: 18px;
        }

        img {
            width: 17rem;
            height: 4rem;
        }

        .tops {
            position: absolute;
            left: 10px;
        }

        .bottom {
            position: absolute;
            top: 3rem;
            width: 12rem;
            left: 10px;

        }
    }

    .mid {
        display: flex;
        flex-direction: column;
        gap: 2.5rem;
        justify-content: center;
        align-items: center;
        position: relative;
        top: 0;

        img {
            rotate: 269deg;
            height: 3rem;
            width: 7rem;
        }

        div {
            color: #fff;
            font-size: 18px;
        }

        em {
            color: #f0c059;
            font-size: 18px;
        }
    }

    .right {
        position: relative;
        top: 3.5rem;
        right: 3.5rem;
        div {
            color: #fff;
            font-size: 18px;
        }

        em {
            color: #f0c059;
            font-size: 18px;
        }

        img {
            rotate: 220deg;
            width: 18rem;
            height: 4rem;
        }

        .tops {
            position: absolute;
            left: 76px;
            rotate: 40deg;
        }

        .bottom {
            position: absolute;
            left: 32px;
            rotate: 40deg;
            width: 12rem;
            top: 46px;
        }
    }
}

.thirdDiv {
    display: flex;
    position: relative;
    grid-area: 2rem;
    justify-content: center;

    .cardContainerP {
        display: flex;
        justify-content: space-between;
        width: 85%;
        gap: 1rem;

        .card {
            border: 1px solid #f0c059;
            width: 15rem;
            height: 18rem;
            border-radius: 10px;

            .agentIcon {
                display: flex;
                align-items: center;
                justify-content: center;

                .agentContainer {
                    position: relative;
                    top: -3rem;
                }
            }
            .conetnt{
                color: white;
                text-align: center;
                border-bottom: 1px solid #f0c059;
                height: 8rem;
            }
            .footerDiv{
                text-align: center;
                padding: 1rem;
                color: #fff;
                font-size: 18px;
                font-weight: 600;
            }
        }
    }

}

.agentContainer {
    max-width: 6rem;
    max-height: 6rem;
    border: 3px solid #f0c059;
    border-radius: 80%;
    position: relative;

    .agentType {
        position: absolute;
        border: 1px solid #f0c059;
        background-color: #f0c059;
        border-radius: 80%;
        height: 2rem;
        width: 2rem;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        right: -3px;
        top: 65px;
    }

    img {
        width: 100%;
        height: 100%;
    }
}
em {
    color: #f0c059;
    font-size: 18px;
}

.fourthDiv{
    margin-top: 1rem;
    height: 15rem;
    text-align: center;
    display: flex;
    align-items: center;
    align-content: stretch;
    justify-content: space-evenly;
    padding-bottom:3rem ;
    flex-direction: column;
    .leftArrow{
        position: relative;
        rotate: -90deg;
        height: 2.5rem;
        left: 4rem;
    }
    .centerArrow{
        rotate: -159deg;
        height: 3.5rem;
        width: 15rem;
        left: -7rem;
        position: relative;
    }
    .rightArrow{
        position: relative;
        rotate: -90deg;
        height: 2.5rem;
        right: 4rem;
    }
    p{
        color: #fff;
        display: flex;
        flex-direction: column;
        font-size: 18px;
    }
}
