
    .overCard{
      border: 3px solid;
      border-color: #f0c05982;
      background: #25282e;
      color: #f0c059;
      display: flex;
      align-items: center;
      box-shadow: 0px 0px 5px 0px rgba(240,192,89,0.59);
      border-radius: 7px;
      padding: 20px;
      gap: 10px;
      width: fit-content;
      .info{
        display: flex;
        gap: 10px;
      }
    }