.headerContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 4rem;
  background-color: #1C1E23;
  gap: 10px;
  border-radius: 7px;
  position: relative;
}
.headerContainer .back {
  max-width: -moz-max-content;
  max-width: max-content;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  left: 1rem;
}
.headerContainer .back span {
  color: #fff;
  font-size: 22px;
  font-weight: 600;
  font-style: italic;
  letter-spacing: 2px;
}
.headerContainer .titleWing {
  width: 5rem;
}
.headerContainer .titleWing:nth-child(3) {
  rotate: 180deg;
}
.headerContainer .titleWing2 {
  width: 5rem;
}
.headerContainer .titleWing2:nth-child(4) {
  rotate: 180deg;
}
.headerContainer .headerTitle {
  font-size: 24px;
  font-weight: bold;
  color: #fff;
}/*# sourceMappingURL=header.css.map */