.headerContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 4rem;
    background-color: #1C1E23;
    gap: 10px;
    border-radius: 7px;
    position: relative;

    .back {
        max-width: max-content;
        display: flex;
        align-items: center;
        cursor: pointer;
        position: absolute;
        left: 1rem;

        span {
            color: #fff;
            font-size: 22px;
            font-weight: 600;
            font-style: italic;
            letter-spacing: 2px;
        }
    }

    .titleWing {
        width: 5rem;

        &:nth-child(3) {
            rotate: 180deg;
        }
    }

    .titleWing2 {
        width: 5rem;

        &:nth-child(4) {
            rotate: 180deg;
        }
    }

    .headerTitle {
        font-size: 24px;
        font-weight: bold;
        color: #fff;
    }
}