.promoOverviewMainContainer {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    .header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        gap: 10px;
        border-radius: 7px;

        .titleWing {
            width: 5rem;

            &:nth-child(3) {
                rotate: 180deg;
            }
        }

        .headerTitle {
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }

    .promoOverviewContainer {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        background-color: #1C1E23;
        padding: 1rem;
        border-radius: 7px;
        // margin-bottom: 1.5rem;

        .dateInputBox {
            .dateButton {
                background-color: #f0c059;
                color: #874404;
                font-weight: 600;
            }
            
            .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
                border-color: #f0c0599e !important;
            }

            .Mui-focused {
                .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
                    border-color: #f0c059 !important;
                }
            }

            .css-1jy569b-MuiFormLabel-root-MuiInputLabel-root,
            .css-1n4twyu-MuiInputBase-input-MuiOutlinedInput-input,
            .css-19qh8xo-MuiInputBase-input-MuiOutlinedInput-input,
            .css-1yq5fb3-MuiButtonBase-root-MuiIconButton-root,
            .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon,
            .css-bpeome-MuiSvgIcon-root-MuiSelect-icon,
            .css-jedpe8-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input,
            .css-1pysi21-MuiFormLabel-root-MuiInputLabel-root {
                color: #fff;
            }
        }

        .contentContainerBox {
            display: flex;
            flex-direction: column;
            justify-content: center;

            .miniBoxContainer {
                .miniBox {
                    background-color: rgb(25, 27, 31);
                    border: 1px solid;
                    border-color: #f0c0599e;
                    border-radius: 7px;
                    width: 10.05rem;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    text-align: center;
                    padding: 1rem;

                    .miniBoxLable {
                        font-style: normal;
                        color: #fff;
                    }

                    .miniBoxCurrent {
                        font-style: italic;
                        font-weight: 600;
                        color: rgb(8, 173, 8);
                    }

                    .miniBoxContent {
                        font-style: normal;
                        font-weight: 600;
                        color: #f0c059;
                    }
                }

                @media (min-width: 900px) {
                    .css-1r8g63t-MuiStack-root {
                        gap: 13px;
                    }
                }
            }
        }
    }

    .tableContainerOverView {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        .tableContainer {
            display: table;
            width: 100%;
            border-spacing: 0;
            border-collapse: separate;
            overflow: auto;

            .tableHead {
                display: table-header-group;
                border-radius: 5px;
                background-color: #202329;
                font-size: 14px;

                .tableRow {
                    display: table-row;

                    &:first-child {
                        .tableCell {
                            &:first-child {
                                border-top-left-radius: 7px;
                                border-bottom-left-radius: 7px;
                                border: 2px solid;
                                border-color: #f0c059a5;
                                border-right: none;
                            }

                            &:last-child {
                                border-top-right-radius: 7px;
                                border-bottom-right-radius: 7px;
                                border: 2px solid;
                                border-color: #f0c059a5;
                                border-left: none;
                            }

                            &:not(:first-child):not(:last-child) {
                                border: 2px solid;
                                border-color: #f0c059a5;
                                border-left: none;
                                border-right: none;
                            }
                        }
                    }

                    .tableCell {
                        display: table-cell;
                        color: white;
                        text-align: center;
                        font-weight: bold;
                        padding: 1.2rem;
                        border-right: none;
                        border-left: none;
                    }
                }
            }

            .tableBody {
                display: table-row-group;
                font-size: 13px;

                .tableRow {
                    display: table-row;
                    transition: all 0.3s ease;

                    &:nth-child(even) {
                        background-color: #202329;

                        .tableCell {
                            &:first-child {
                                border-top-left-radius: 7px;
                                border-bottom-left-radius: 7px;
                                border: thin solid;
                                border-color: #f0c0596e;
                                border-right: none;
                            }

                            &:last-child {
                                border-top-right-radius: 7px;
                                border-bottom-right-radius: 7px;
                                border: thin solid;
                                border-color: #f0c0596e;
                                border-left: none;
                            }

                            &:not(:first-child):not(:last-child) {
                                border: thin solid;
                                border-color: #f0c0596e;
                                border-left: none;
                                border-right: none;
                            }
                        }
                    }

                    .tableCell {
                        display: table-cell;
                        text-align: center;
                        color: white;
                        padding: .8rem;
                    }
                }
            }
        }

        .pagination {
            padding: 0 !important;
            display: flex;
            justify-content: center;
            align-items: center;

            li {
                button {
                    color: white !important;
                    font-size: 18px !important;

                }

                .css-1v2lvtn-MuiPaginationItem-root {
                    color: white;
                }

                .css-19micn4-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected {
                    background-color: #f0c059;
                    color: #874404 !important;
                    font-weight: 600;
                }
            }
        }
        .noData{
            display: flex;
            align-items: center;
            justify-content: center;
            .noDataLabel{
                font-weight: 600;
                font-style: italic;
                color: #f0c059;
            }
        }
    }
}

