.sideBar {
    overflow: auto;
    // display: grid;
    // grid-template-columns: 1fr;
    padding: 10px;
    width: 20rem;
    transition:  width 200ms ease, grid-template-columns 200ms ease;
    padding-top: 10px;
    height: 91vh;
    &.active {
        // grid-template-columns: 0fr;
        padding-right: 0px;
        padding-left: 0px;
        width: 0%;
        transition: width 200ms ease, grid-template-columns 200ms ease;

    }

    &::-webkit-scrollbar {
        display: none;
    }

    .sidebarContainer {
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: clamp(0.5px, 0.5vw, 10px);
        min-width: 0;
        overflow: hidden;

        .gameContainer {
            display: grid;
            grid-template-columns: auto auto;
            gap: clamp(.5px, .5vw, 10px);
            width: 100%;

            .gameBox {
                background-color: #22242A;
                width: 6.45vw;
                height: 9vh;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                color: #ADB6C3;
                font-weight: 500;
                border-radius: 7px;
                cursor: pointer !important;
                padding: 10px;
                text-align: center;
                cursor: pointer !important;
                font-weight: 600;

                .imageContainer {
                    .gameImage {
                        width: 2vw;
                    }
                }

                .labelContainer {
                    font-size: clamp(5px, 1vw, 14px);
                }

                &.active {
                    background-color: #f0c059dd;
                    color: #874404;
                }

                &:hover {
                    color: #874404;
                    background-color: #f0c059dd;
                }
            }

            // .active-scroll-spy {
            //     background-color: #f0c059dd;
            //     color: #874404;
            // }
        }

        .multimediaContainer {
            display: flex;
            flex-direction: column;
            gap: clamp(.5px, .5vw, 10px);
            width: 100%;

            .playerConfainer {
                background-color: #22242A;
                height: 6vh;
                width: 100%;
                border-radius: 7px;

                .playerContent {
                    margin: 10px;
                    font-size: clamp(5px, 1vw, 16px);
                }
            }

            .mediaRecordContainer {
                background-color: #22242A;
                height: 6vh;
                width: 100%;
                border-radius: 7px;

                .playerContent {
                    margin: 10px;
                    font-size: clamp(5px, 1vw, 16px);
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    font-weight: 600;
                    cursor: pointer;

                    label {
                        cursor: pointer;
                        color: #F0C059;
                    }
                }

                &:hover {
                    background-color: #2f313a;
                }

                a {
                    text-decoration: none;
                    color: #F0C059;
                }
            }
        }

        .linksContainer {
            width: 100%;
            height: auto;
            border-radius: 7px;
            background-color: #22242A;

            .linkContainer {
                display: grid;
                grid-template-columns: auto auto;
                justify-content: center;
                gap: 10px;
                margin: 13px 10px 10px 10px;
                height: 100%;

                .pageLink {
                    position: relative;

                    &:hover {
                        scale: 1.01;
                    }

                    .linkLabel {
                        position: absolute;
                        margin: 5px 8px;
                        font-size: clamp(5px, 1vw, 16px);
                        font-weight: 400;
                        color: #ffff;
                        word-wrap: break-word;
                        cursor: pointer;
                    }

                    .imageContainer {
                        background-color: #17181d;
                        border-radius: 8px;
                        height: 8vh;
                        width: 5.7vw;
                        cursor: pointer;

                        .linkImage {
                            height: 8vh;
                            width: 5.7vw;
                        }
                    }
                }
            }

            .pageLinkConvide {
                position: relative;
                margin: 10px 10px 15px 10px;
                cursor: pointer;

                .linkLabel {
                    position: absolute;
                    margin: 5px 11px;
                    font-size: clamp(5px, 1vw, 16px);
                    font-weight: 400;
                    color: #ffff;
                    word-wrap: break-word;
                    cursor: pointer;
                }

                &:hover {
                    scale: 1.01;
                }

                .imageContainer {
                    display: flex;
                    justify-content: center;
                    border-radius: 8px;
                    height: 6.8vh;
                    width: 100%;
                    height: auto;

                    .linkImage {
                        width: 97%;
                        height: 8vh;
                    }
                }
            }
        }

        .sidebarFooterContainer {
            display: flex;
            flex-direction: column;
            gap: 15px;
            width: 100%;
            margin-left: 1rem;
            margin-top: .5rem;
            height: 30vh;

            .footerItemContainer {
                display: flex;
                align-items: center;
                gap: 10px;
                color: rgb(170, 166, 166);
                cursor: pointer;
                width: 90%;

                &:hover {
                    color: #F0C059;
                }

                .itemIcon {
                    height: 3.5vh;
                    width: 2vw;
                }

                .iconLabel {
                    font-size: clamp(5px, 1vw, 16px);
                    font-weight: 500;
                    cursor: pointer;
                }

                // .languageSelect {
                //     padding: 10px;
                //     background-color: #1c1e23;
                //     border: 2px solid;
                //     border-color: #f0c05965; 
                //     border-radius: 4px; 
                //     color: rgb(170, 166, 166); 
                //     width: 100%;
                //     font-size: clamp(5px, 1vw, 16px); 
                //     cursor: pointer; 
                //     -webkit-appearance: none; 
                //     -moz-appearance: none; 
                //     appearance: none; 
                //     background-image: url('data:image/svg+xml;utf8,<svg fill="%23adb6c3" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M7 10l5 5 5-5z"/></svg>'); // Custom arrow icon
                //     background-repeat: no-repeat;
                //     background-position: right 10px center;
                //     background-size: 20px;
                //     padding-right: 2rem; 
                //     background-position: right 0.5rem center; 
                //     background-size: 1rem;
                //     &:hover {
                //       background-color: #2f313a;
                //       color: #F0C059;
                //     }
                // }
                .languageOption {
                    background-color: #1c1e23;
                    color: #fff;
                    padding: 0.5rem;

                    &:hover,
                    &:focus,
                    &:checked {
                        background-color: #2f313a;
                        color: #F0C059;
                    }
                }
            }

            a {
                text-decoration: none;
            }
        }
    }
}

@keyframes sideBarAnimation {
    0% {
        width: 0;
    }

    100% {
        width: 200px;
    }
}