.securityMainContainer{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .usernameContainer,
    .loginPasswordContainer,
    .withdrawalPasswordContainer,
    .phoneNumberContainer,
    .emailContainer,
    .lineContainer,
    .facebookContainer{
      background-color: #1C1E23;
      border-radius: 7px;
      padding: 1rem;
      display: flex;
      align-items: center;
      &:last-child{
        margin-bottom: 1.5rem;
      }
      .iconContainer{
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 10%;
      }
      .labelContainer{
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        width: 100%;
        border: thin;
        color: #f0c059;
      }
      .buttonContainer{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: end;
        width: 100%;
        .profileButton{
          color: #f0c059;
          border: thin solid;
          width: 7rem;
        }
        .realNameLabel{
          color: #f0c059;
          font-style: italic;
          font-size: 14px;
        }
      }
    }
}