.cards {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
}

.cardContainer .cardHeader {
  color: white;
  margin-bottom: 0.5rem;
}
.cardContainer .cardBody {
  display: table;
  width: 100%;
  border-spacing: 0;
  border-collapse: separate;
  border: thin solid;
  border-top-left-radius: 8px;
  border-top-right-radius: 9px;
}
.cardContainer .cardBody .subTitles {
  display: table-header-group;
  background-color: #f0c059;
  font-size: 14.5px;
}
.cardContainer .cardBody .subTitles .rows {
  display: table-row;
  border: thin solid #f0c059;
}
.cardContainer .cardBody .subTitles .rows:first-child .cell:first-child {
  border-top-left-radius: 7px;
  border: 2px solid;
  border-color: rgba(240, 192, 89, 0.6470588235);
  border-right: none;
}
.cardContainer .cardBody .subTitles .rows:first-child .cell:last-child {
  border-top-right-radius: 7px;
  border: 2px solid;
  border-color: rgba(240, 192, 89, 0.6470588235);
  border-left: none;
}
.cardContainer .cardBody .subTitles .rows:first-child .cell:not(:first-child):not(:last-child) {
  border: 2px solid;
  border-color: rgba(240, 192, 89, 0.6470588235);
  border-left: none;
  border-right: none;
}
.cardContainer .cardBody .subTitles .rows .cell {
  display: table-cell;
  padding: 0.7rem 3px;
  color: white;
  font-weight: 600;
}
.cardContainer .cardBody .subBody {
  display: table-row-group;
  font-size: 13.5px;
  background-color: #1c1e23;
  border: thin solid;
}
.cardContainer .cardBody .subBody .rows {
  display: table-row;
  transition: all 0.3s ease;
  border: thin solid;
}
.cardContainer .cardBody .subBody .rows:nth-child(even) {
  background-color: #202329;
}
.cardContainer .cardBody .subBody .rows:nth-child(even) .cell:first-child {
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  border: thin solid;
  border-color: rgba(240, 192, 89, 0.431372549);
  border-right: none;
}
.cardContainer .cardBody .subBody .rows:nth-child(even) .cell:last-child {
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  border: thin solid;
  border-color: rgba(240, 192, 89, 0.431372549);
  border-left: none;
}
.cardContainer .cardBody .subBody .rows:nth-child(even) .cell:not(:first-child):not(:last-child) {
  border: thin solid;
  border-color: rgba(240, 192, 89, 0.431372549);
  border-left: none;
  border-right: none;
}
.cardContainer .cardBody .subBody .rows .cell {
  display: table-cell;
  text-align: center;
  color: white;
  padding: 0.7rem 3px;
}/*# sourceMappingURL=minicard.css.map */