* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.mainBlur {
  position: absolute;
  width: 100vw;
  height: 100vh;
  -webkit-backdrop-filter: blur(5px) brightness(0.6);
          backdrop-filter: blur(5px) brightness(0.6);
  z-index: 1;
  transition: 0.6s ease-in;
  display: none;
}

#endingTime {
  position: absolute;
}

#folder {
  position: absolute;
  left: 36vw;
  top: -60vh;
  cursor: pointer;
}

#timeBar {
  display: none;
}

#close {
  position: absolute;
  left: 58vw;
  top: -30vh;
  cursor: pointer;
  z-index: 2;
  height: 50px;
}

#close1 {
  position: absolute;
  right: -3vw;
  height: 50px;
  top: -5vh;
  cursor: pointer;
  z-index: 2;
}

.mainRewards {
  position: absolute;
  left: 41vw;
  top: 25vh;
}

.openFolderDivs {
  position: relative;
}

.openFolderAbsolute {
  position: relative;
}

/* .reward {
  display: flex;
  position: absolute;
  top: 35px;
  left: 97px;
} */
.gxtitle {
  position: absolute;
  top: 20px;
  width: 100%;
  left: 1vw;
  text-align: center;
}

/* .gxtitle img {
  height: 25px;
} */
#openFolder {
  left: 2vw;
}

small {
  color: #ba5a12;
  font-size: 24px;
  margin: 0 8px 0 8px;
  line-height: 10px;
}

.winmoney {
  position: absolute;
  top: 70px;
  left: 0px;
  font-size: 40px;
  color: #d63231;
  font-weight: 800;
  text-align: center;
  width: 100%;
}

.slideDown {
  transition: 0.6s ease;
  transform: translateY(150%);
}

.slideDownClose {
  transition: 0.6s ease;
  transform: translateY(55vh);
}

.main {
  background-image: url("/public/redfolderImages/newBg.jpg");
  background-repeat: no-repeat;
  background-size: 100% 110%;
  background-position: center bottom;
  height: 59vh;
  position: relative;
}

.endingTimeText {
  position: absolute;
  top: 32vh;
  right: 24vw;
  color: #51effd;
}

#wenan {
  position: absolute;
  left: 33vw;
  top: -10vh;
}

#wenan2 {
  position: absolute;
  left: 68vw;
  top: 20vh;
  height: 30px;
}

#endingTime {
  top: 24vh;
  right: 23vw;
}

#time {
  position: absolute;
  left: 66vw;
  top: 26vh;
  width: 15vw;
}

#getItNow {
  cursor: pointer;
  top: 42vh;
}

#btn {
  position: absolute;
  top: 40vh;
  right: 39vw;
  height: 8vh;
}

.endingTimeNum {
  position: absolute;
  display: none;
  color: #51effd;
}

.main span {
  font-weight: 100px;
}

h1 {
  margin: 30px 0;
}

p {
  padding: 0 10px;
}

.awardList {
  height: 41vh;
  background-color: black;
  display: flex;
  justify-content: center;
}

.mainList {
  background-color: #620815;
  width: 70vw;
  height: 30vh;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.awardHeader {
  display: flex;
  align-items: center;
  color: #fbd4a0;
}

.awardHeader img {
  height: 50px;
}

#dollar {
  position: absolute;
  left: 47.5vw;
  top: 38vh;
}

.winners {
  display: flex;
  width: 100%;
  justify-content: space-around;
  color: #e0ced0;
}

.winners .winnerDetails {
  display: flex;
}

.winnerDetails span {
  color: #f5c901;
}

p {
  margin: 0;
  font-size: 13px;
}

h1 {
  font-size: 25px;
}

