.footer {
  width: 100%;
  height: fit-content;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 5%;
  padding-bottom: 1%;
  .footer-link {
    color: #68707B;
    // margin-right: 100px;
    line-height: 2;
    font-size: 18px;
    margin-bottom: 10%;

    p {
      cursor: pointer;
      font-size: 20px;
    }
    p:hover {
      color: white;
    }
    .footer-text {
      width: 30vw;
      p{
        font-size: 16px;
        text-align: justify;
      }
 
    }
   .header-p {
    color: #ADB6C3;
   }
  }
  #link-social {
    font-size: 15px;
    display: flex;
    flex-direction: column;
  }
  .footer-container {
    display: flex;
    justify-content: center;
    display: flex;
    gap: 79px;
  }
  .footer-social img {
    height: 50px;
    margin-right: 20px;
    cursor: pointer;
  }
  .footer-social{
    display: flex;
    flex-wrap: wrap;
    width: 1rem;
    gap: 1rem;
    width: 37rem;
    padding-bottom: 1rem;
  }
  .footer-bottom-img {
    display: flex;
    gap: 3.5rem;
    border-top: 1px solid rgb(107, 107, 107);
    padding-top: 20px;
    margin-right: 15px;

  }
  .footer-bottom-img img {
    height: 30px;
  }
  .footer-container2 {
    text-align: center;
    p {
      color: #68707B;
      margin-top: 3%;
    }
  }
}
