  padding-top: 2rem;
  min-height: 1072px;
  position: relative;
}
.mainLayout .mainContent .centerContent .contentContainer .envelopeContainer {
  position: fixed;
  right: 54px;
  bottom: 20vh;
  background: linear-gradient(180deg, #ed812d, #feba5a);
  border-radius: 55px;
  cursor: pointer;
  height: 3.5rem;
  width: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 43rem;
}
