  width: 66vw;
  margin: auto;
  height: 100%;
}
.mainLayout .mainContent .centerContent .contentContainer .scrolltoTop {
  position: fixed;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  bottom: 35px;
  right: 3.8rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mainLayout .mainContent .centerContent .contentContainer .gift-container {
  background: linear-gradient(180deg, #ed812d, #feba5a);
  cursor: pointer;
  border-radius: 50%;
  position: fixed;
  right: 55px;
  bottom: 100px;
  height: 3.5rem;
  width: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mainLayout .mainContent .centerContent .contentContainer .gift-container img {
  position: relative;
  height: 40px;
}

