.cards{
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  .cardContainer{
    .cardHeader{
      color: white;
      margin-bottom: .5rem;
    }
    .cardBody{
      display: table;
      width: 100%;
      border-spacing: 0;
      border-collapse: separate;
      border: thin solid;
      border-top-left-radius: 8px;
      border-top-right-radius: 9px;
      .subTitles{
        display: table-header-group;
        background-color: #f0c059;
        font-size: 14.5px;
        .rows{
          display: table-row;
          border: thin solid #f0c059;
          &:first-child {
            .cell {
                &:first-child {
                    border-top-left-radius: 7px;
                    border: 2px solid;
                    border-color: #f0c059a5;
                    border-right: none;
                }
  
                &:last-child {
                    border-top-right-radius: 7px;
                    border: 2px solid;
                    border-color: #f0c059a5;
                    border-left: none;
                }
  
                &:not(:first-child):not(:last-child) {
                    border: 2px solid;
                    border-color: #f0c059a5;
                    border-left: none;
                    border-right: none;
                }
            }
          }
          .cell{
            display: table-cell;
            padding: .7rem 3px;
            color: white;
            font-weight: 600;
          }
        }
      }
      .subBody{
        display: table-row-group;
        font-size: 13.5px;
        background-color: #1c1e23;
        border: thin solid;
        .rows{
          display: table-row;
          transition: all 0.3s ease;
          border: thin solid;
          &:nth-child(even) {
              background-color: #202329;
              .cell {
                  &:first-child {
                      border-top-left-radius: 7px;
                      border-bottom-left-radius: 7px;
                      border: thin solid;
                      border-color: #f0c0596e;
                      border-right: none;
                  }
  
                  &:last-child {
                      border-top-right-radius: 7px;
                      border-bottom-right-radius: 7px;
                      border: thin solid;
                      border-color: #f0c0596e;
                      border-left: none;
                  }
  
                  &:not(:first-child):not(:last-child) {
                      border: thin solid;
                      border-color: #f0c0596e;
                      border-left: none;
                      border-right: none;
                  }
              }
          }
          .cell {
              display: table-cell;
              text-align: center;
              color: white;
              padding: .7rem 3px;
          }
      }
      }
    }
  }