.content{
    height: 100%;
    .slider{
        position: relative;

        .leftArrow{
            position: absolute;
            color: white;
            height: 40px;
            width: 40px;
            top: 3rem;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.192);
            border-radius: 50%;
            cursor: pointer;
            z-index: 20;
        }
        .rightArrow{
            position: absolute;
            color: white;
            height: 40px;
            width: 40px;
            top: 3rem;
            right: 0;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.192);
            border-radius: 50%;
            cursor: pointer;
        }
        .slider-content{
            display: flex;
            margin-top: 2rem;
            
            .item{
                max-width: 40rem;
                cursor:pointer;
                img{
                    width: 100%;
                    height: 100%;
                    border-radius: 10px;
                }
                &:nth-child(1){
                    scale: 1.5;
                    filter: blur(1.5px);
                    position: relative;
                    left: 7rem;
                }
                &:nth-child(2){
                    z-index: 2;
                    scale: 1.8;
                    
                }
                &:nth-child(3){
                    filter: blur(1.5px);
                    scale: 1.5;
                    position: relative;
                    right: 7rem;
                }
            }
        }
    }
    .playList{
        margin-top: 5rem;
        .playListContainer{
            background-color: #1C1E23;
            border-radius: 5px;
            padding: 2px 5px;
            display: flex;
            .audioIcon{
                width: 2rem;
                height:2rem;
                margin: auto;
            }
            .marqueeContainer{
                overflow: hidden;
                display: flex;
                align-items: center;
                    
                .marqueeText{
                    color: red;
                    white-space: nowrap;
                    will-change: transform;
                    font-size: 19px;
                    font-weight: bold;
                    animation: marquee 30s linear infinite;
                }
            }
            .messageIcon{
                color: #adb6c4;
                height: 2.5rem;
                width: 2.5rem;
            }
        }
    }
    .gameContainers{
        background-color: #1C1E23;
        height: 100%;
        margin-top: 1rem;
        border-radius:15px;
        .gameTabs{
            display: flex;
            overflow: auto;
            border-bottom: 2px solid #313843;
            padding: .5rem .5rem 0 .5rem;
           img{
            width: 11rem;
            height: 15rem;
        
           }
            // TABS
            .gameIcons{
                display: flex;
                width: 100%;
                justify-content: start;
                gap: 37px;
                .gameBox{
                    color: #adb6c4;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                    &.active{
                        border-bottom: 2px solid;
                        border-color: #F0C059;
                        padding-bottom: 5px;
                        color: #F0C059;
                    }
                    .imageContainer{
                        height: 2.5rem;
                        width: 2.5rem;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .labelContainer{
                        width: max-content !important;
                        text-align: center;
                        word-wrap: break-word;
                    }
                    
                }
            }
            // END TABS
        }
        .gamesMain{
            margin-bottom: 1rem;
            .gameItems{
                padding: 0px 15px;
                margin: 2rem 0px;
                .imageContainer{
                    display: flex;
                    align-items: center;
                    gap:1rem;
                    margin-bottom: 1rem;
                    justify-content: space-between;
                    .left{
                        display: flex;
                        gap:1rem;
                        .labelContainer{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            span{
                              font-size: 20px;
                              font-weight: bold;
                              color: white;
                            }
                          }
                    }
                   .right{
                    color: #F0C059;
                    margin-right: 4rem;
                    cursor: pointer;
                    // font-size: 1rem;
               
                   }
                }
                
                .gameBox{
                    display: flex;
                    align-items: center;
                    gap: 12px;
                    
                    .imageContainer{
                        height: 3rem;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .labelContainer{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                      span{
                        font-size: 20px;
                        font-weight: bold;
                        color: white;
                      }
                    }
                }
                .games{
                    display: flex;
                    flex-wrap: wrap;
                    gap: 20px;
                    row-gap: 20px;
                    column-gap: 20px;
                    padding: 10px 0 0 50px;
                    .imgGames{
                        cursor: pointer;
                        width: 11rem;
                        position: relative;
                        .playBtn{
                            position: absolute;
                            bottom: 0;
                            top: 0;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            height: 100%;
                            width: 100%;
                        .icon{
                            width: 5rem !important;
                            height: 5rem !important;
                            opacity: 0;
                            transition:opacity 400ms ease ;
                            cursor: pointer;
                        }
                    }
                    &:hover{
                        .playBtn{
                            position: absolute;
                            bottom: 0;
                            top: 0;
                            background-color: #05050559;
                            border-radius:25px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            .icon{
                                opacity: 1;
                                transition:opacity 400ms ease ;
                            }
                        }
                        
                    }
                    }
                    span{
                        height: 14rem !important;
                        width: 100% !important;
                    }
                    .gameCard{
                       
                    width: 100%;
                    height: 100%;
                        border-radius:25px;
                        background-repeat: no-repeat;
                        background-size:100% 100%;
                        cursor: pointer;
                        position: relative;
                       
                        &:hover{
                            .backDrop{
                                position: absolute;
                                bottom: 0;
                                top: 0;
                                background-color: #05050559;
                                border-radius:25px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                              
                                height: 100%;
                                width: 100%;
                                .icon{
                                    opacity: 1;
                                    transition:opacity 400ms ease ;
                                }
                            }
                            
                        }
                        .backDrop{
                            position: absolute;
                            bottom: 0;
                            top: 0;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            height: 100%;
                            width: 100%;
                        .icon{
                            
                            width: 5rem;
                            opacity: 0;
                            transition:opacity 400ms ease ;
                        }
                    }
                    }
                }
            }
           
        }
    }
}
@keyframes marquee {
    from { transform: translateX(0); }
    to { transform: translateX(-50%); }
}