.promoListMainContainer{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        gap: 10px;
        border-radius: 7px;

        .titleWing {
            width: 5rem;

            &:nth-child(3) {
                rotate: 180deg;
            }
        }

        .headerTitle {
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }
    .promoListMainBody{
        background-color: #1C1E23;
        border-radius: 7px;
        padding: 1rem;
        margin-bottom: 1.5rem;
        .promoListOuterBox{
            display: flex;
            flex-direction: column;
            gap: 10px;
            align-items: center;
            justify-content: center;
            background-color: #17191d;
            border-radius: 7px;
            border: 2px solid;
            border-color: #f0c0595f;
            padding: 1rem;
            margin: 1rem;
            width: 20rem;
            height: 20rem;
            .promoLabelBox{
                display: flex;
                gap: 5px;
                color: #fff;
                .promoTitle{
                    color: #fff;
                }
                .vipContent,
                .timeContent,
                .usernameContent,
                .promoteCodeContent,
                .promoteLinkContent,
                .encryptedPromoLinkContent,
                .promoPageContent,
                .visitorContent,
                .registersContent{
                    color: #f0c059;
                }
            }
            .promoLinkBox{
                display: flex;
                flex-direction: column;
                align-items: center;
                .promoteLinkContent,
                .encryptedPromoLinkContent{
                    font-style: italic;
                    color: rgb(25, 153, 25);
                }
            }
            .imageBox{
                .promoImage{
                    width: 6rem;
                }
            }
            .lineDiv{
                width: 100%;
                border: thin solid;
                border-color: #f0c0595f;
            }
            .regButton{
                background-color: #f0c059;
                color: #874404;
                font-weight: 600;
            }
            .css-i4bv87-MuiSvgIcon-root{
                color: #f0c059 !important;
            }
        }
    }
}