.sideBar {
  overflow: auto;
  padding: 10px;
  width: 20rem;
  transition: width 200ms ease, grid-template-columns 200ms ease;
  padding-top: 10px;
  height: 91vh;
}
.sideBar.active {
  padding-right: 0px;
  padding-left: 0px;
  width: 0%;
  transition: width 200ms ease, grid-template-columns 200ms ease;
}
.sideBar::-webkit-scrollbar {
  display: none;
}
.sideBar .sidebarContainer {
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: clamp(0.5px, 0.5vw, 10px);
  min-width: 0;
  overflow: hidden;
}
.sideBar .sidebarContainer .gameContainer {
  display: grid;
  grid-template-columns: auto auto;
  gap: clamp(0.5px, 0.5vw, 10px);
  width: 100%;
}
.sideBar .sidebarContainer .gameContainer .gameBox {
  background-color: #22242A;
  width: 6.45vw;
  height: 9vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ADB6C3;
  font-weight: 500;
  border-radius: 7px;
  cursor: pointer !important;
  padding: 10px;
  text-align: center;
  cursor: pointer !important;
  font-weight: 600;
}
.sideBar .sidebarContainer .gameContainer .gameBox .imageContainer .gameImage {
  width: 2vw;
}
.sideBar .sidebarContainer .gameContainer .gameBox .labelContainer {
  font-size: clamp(5px, 1vw, 14px);
}
.sideBar .sidebarContainer .gameContainer .gameBox.active {
  background-color: rgba(240, 192, 89, 0.8666666667);
  color: #874404;
}
.sideBar .sidebarContainer .gameContainer .gameBox:hover {
  color: #874404;
  background-color: rgba(240, 192, 89, 0.8666666667);
}
.sideBar .sidebarContainer .multimediaContainer {
  display: flex;
  flex-direction: column;
  gap: clamp(0.5px, 0.5vw, 10px);
  width: 100%;
}
.sideBar .sidebarContainer .multimediaContainer .playerConfainer {
  background-color: #22242A;
  height: 6vh;
  width: 100%;
  border-radius: 7px;
}
.sideBar .sidebarContainer .multimediaContainer .playerConfainer .playerContent {
  margin: 10px;
  font-size: clamp(5px, 1vw, 16px);
}
.sideBar .sidebarContainer .multimediaContainer .mediaRecordContainer {
  background-color: #22242A;
  height: 6vh;
  width: 100%;
  border-radius: 7px;
}
.sideBar .sidebarContainer .multimediaContainer .mediaRecordContainer .playerContent {
  margin: 10px;
  font-size: clamp(5px, 1vw, 16px);
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
  cursor: pointer;
}
.sideBar .sidebarContainer .multimediaContainer .mediaRecordContainer .playerContent label {
  cursor: pointer;
  color: #F0C059;
}
.sideBar .sidebarContainer .multimediaContainer .mediaRecordContainer:hover {
  background-color: #2f313a;
}
.sideBar .sidebarContainer .multimediaContainer .mediaRecordContainer a {
  text-decoration: none;
  color: #F0C059;
}
.sideBar .sidebarContainer .linksContainer {
  width: 100%;
  height: auto;
  border-radius: 7px;
  background-color: #22242A;
}
.sideBar .sidebarContainer .linksContainer .linkContainer {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: center;
  gap: 10px;
  margin: 13px 10px 10px 10px;
  height: 100%;
}
.sideBar .sidebarContainer .linksContainer .linkContainer .pageLink {
  position: relative;
}
.sideBar .sidebarContainer .linksContainer .linkContainer .pageLink:hover {
  scale: 1.01;
}
.sideBar .sidebarContainer .linksContainer .linkContainer .pageLink .linkLabel {
  position: absolute;
  margin: 5px 8px;
  font-size: clamp(5px, 1vw, 16px);
  font-weight: 400;
  color: white;
  word-wrap: break-word;
  cursor: pointer;
}
.sideBar .sidebarContainer .linksContainer .linkContainer .pageLink .imageContainer {
  background-color: #17181d;
  border-radius: 8px;
  height: 8vh;
  width: 5.7vw;
  cursor: pointer;
}
.sideBar .sidebarContainer .linksContainer .linkContainer .pageLink .imageContainer .linkImage {
  height: 8vh;
  width: 5.7vw;
}
.sideBar .sidebarContainer .linksContainer .pageLinkConvide {
  position: relative;
  margin: 10px 10px 15px 10px;
  cursor: pointer;
}
.sideBar .sidebarContainer .linksContainer .pageLinkConvide .linkLabel {
  position: absolute;
  margin: 5px 11px;
  font-size: clamp(5px, 1vw, 16px);
  font-weight: 400;
  color: white;
  word-wrap: break-word;
  cursor: pointer;
}
.sideBar .sidebarContainer .linksContainer .pageLinkConvide:hover {
  scale: 1.01;
}
.sideBar .sidebarContainer .linksContainer .pageLinkConvide .imageContainer {
  display: flex;
  justify-content: center;
  border-radius: 8px;
  height: 6.8vh;
  width: 100%;
  height: auto;
}
.sideBar .sidebarContainer .linksContainer .pageLinkConvide .imageContainer .linkImage {
  width: 97%;
  height: 8vh;
}
.sideBar .sidebarContainer .sidebarFooterContainer {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  margin-left: 1rem;
  margin-top: 0.5rem;
  height: 30vh;
}
.sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer {
  display: flex;
  align-items: center;
  gap: 10px;
  color: rgb(170, 166, 166);
  cursor: pointer;
  width: 90%;
}
.sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer:hover {
  color: #F0C059;
}
.sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer .itemIcon {
  height: 3.5vh;
  width: 2vw;
}
.sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer .iconLabel {
  font-size: clamp(5px, 1vw, 16px);
  font-weight: 500;
  cursor: pointer;
}
.sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer .languageOption {
  background-color: #1c1e23;
  color: #fff;
  padding: 0.5rem;
}
.sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer .languageOption:hover, .sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer .languageOption:focus, .sideBar .sidebarContainer .sidebarFooterContainer .footerItemContainer .languageOption:checked {
  background-color: #2f313a;
  color: #F0C059;
}
.sideBar .sidebarContainer .sidebarFooterContainer a {
  text-decoration: none;
}

@keyframes sideBarAnimation {
  0% {
    width: 0;
  }
  100% {
    width: 200px;
  }
}/*# sourceMappingURL=sidebar.css.map */