.footer {
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 5%;
  padding-bottom: 1%;
}
.footer .footer-link {
  color: #68707B;
  line-height: 2;
  font-size: 18px;
  margin-bottom: 10%;
}
.footer .footer-link p {
  cursor: pointer;
  font-size: 20px;
}
.footer .footer-link p:hover {
  color: white;
}
.footer .footer-link .footer-text {
  width: 30vw;
}
.footer .footer-link .footer-text p {
  font-size: 16px;
  text-align: justify;
}
.footer .footer-link .header-p {
  color: #ADB6C3;
}
.footer #link-social {
  font-size: 15px;
  display: flex;
  flex-direction: column;
}
.footer .footer-container {
  display: flex;
  justify-content: center;
  display: flex;
  gap: 79px;
}
.footer .footer-social img {
  height: 50px;
  margin-right: 20px;
  cursor: pointer;
}
.footer .footer-social {
  display: flex;
  flex-wrap: wrap;
  width: 1rem;
  gap: 1rem;
  width: 37rem;
  padding-bottom: 1rem;
}
.footer .footer-bottom-img {
  display: flex;
  gap: 3.5rem;
  border-top: 1px solid rgb(107, 107, 107);
  padding-top: 20px;
  margin-right: 15px;
}
.footer .footer-bottom-img img {
  height: 30px;
}
.footer .footer-container2 {
  text-align: center;
}
.footer .footer-container2 p {
  color: #68707B;
  margin-top: 3%;
}/*# sourceMappingURL=footer.css.map */