.recordMainContainer{
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
  .recordContainer{
      background-color: #1C1E23;
      padding: 1rem;
      border-radius: 7px;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      .topBox{
          display: flex;
          justify-content: space-between;
          align-items: center;
          .labelBox{
              display: flex;
              gap: 5px;
              align-items: baseline;
              .labelTitle{
                  font-size: 18px;
                  color: #fff;
              }
              .labelContent{
                  font-size: 18px;
                  color: #f0c059;
              }
          }
      }
  }
}