.regManagementMainContainer{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .header{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        gap: 10px;
        border-radius: 7px;
        .titleWing{
            width: 5rem;
            &:nth-child(3){
                rotate: 180deg;
            }
        }
        .headerTitle{
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }
    .regManagementBodyContainer{
        background-color: #1C1E23;
        padding: 1rem;
        border-radius: 7px;
        display: flex;
        justify-content: center;
        .inputSelectBox{
            width: 50%;
            display: flex;
            justify-content: center;
            .registerButton{
                width: 10rem;
                background-color: #f0c059;
                color: #874404;
                font-weight: 600;
            }
            .css-1d3z3hw-MuiOutlinedInput-notchedOutline{
                border-color: #f0c0599e;
            }
            .Mui-focused{
                .css-1d3z3hw-MuiOutlinedInput-notchedOutline{
                    border-color: #f0c059;
                }
            }
            .css-1jy569b-MuiFormLabel-root-MuiInputLabel-root, 
            .css-jedpe8-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input,
            .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon,
            .css-bpeome-MuiSvgIcon-root-MuiSelect-icon,
            .css-1n4twyu-MuiInputBase-input-MuiOutlinedInput-input,
            .css-1pysi21-MuiFormLabel-root-MuiInputLabel-root{
                color: #fff;
            }
        }
        .agentRabateBox{
            display: flex;
            flex-direction: column;
            width: 84%;
            .rebateBoxHeader{
                background-color: #141519;
                padding: 1rem;
                display: flex;
                align-items: center;
                justify-content: center;
                border-top-right-radius: 7px;
                border-top-left-radius: 7px;
                .rebateBoxTitle{
                    color: #fff;
                    font-size: 20px;
                    font-weight: 600;
                }
            }
            .agentRabateBody{
                border-bottom-right-radius: 7px;
                border-bottom-left-radius: 7px;
                background-color: #2e2f37;
                padding: 1rem;
                .inputDownBox{
                    display: grid;
                    grid-template-columns: auto auto;
                    flex-direction: column;
                    justify-content: center;
                    flex-wrap: wrap;
                    gap: 1rem;
                    .inputBox{
                        display: flex;
                        align-items: center;
                        gap: .5rem;
                        background-color: #26262c;
                        width: max-content;
                        padding: .5rem .5rem .5rem 1.5rem;
                        border-radius: 7px;
                        .selectBox{
                            .css-1d3z3hw-MuiOutlinedInput-notchedOutline{
                                border-color: #f0c059;
                            }
                            .css-1jy569b-MuiFormLabel-root-MuiInputLabel-root, 
                            .css-jedpe8-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input,
                            .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon,
                            .css-bpeome-MuiSvgIcon-root-MuiSelect-icon,
                            .css-1n4twyu-MuiInputBase-input-MuiOutlinedInput-input,
                            .css-1pysi21-MuiFormLabel-root-MuiInputLabel-root{
                                color: #fff;
                            }
                        }
                        .imageBox{
                            .iconImage{
                                width: 5.5rem;
                            }   
                        }
                    }
                }
            }
        }
    }
}
.css-17gx806 {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}