.recordMainContainer {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}
.recordMainContainer .recordContainer {
  background-color: #1C1E23;
  padding: 1rem;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.recordMainContainer .recordContainer .topBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.recordMainContainer .recordContainer .topBox .labelBox {
  display: flex;
  gap: 5px;
  align-items: baseline;
}
.recordMainContainer .recordContainer .topBox .labelBox .labelTitle {
  font-size: 18px;
  color: #fff;
}
.recordMainContainer .recordContainer .topBox .labelBox .labelContent {
  font-size: 18px;
  color: #f0c059;
