.coreWalletSectionContainer {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    .coreWallet {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        gap: 10px;
        border-radius: 7px;

        .titleWing {
            width: 5rem;

            &:nth-child(3) {
                rotate: 180deg;
            }
        }

        .coreWalletTitle {
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }

    .balanceBanner {
        display: flex;
        align-items: center;
        background-color: #1C1E23;
        border-radius: 7px;
        padding: 1rem;
        gap: 1rem;

        .balanceTextBox {
            display: flex;
            align-items: center;
            justify-content: start;
            width: max-content;

            .balanceTexts {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 10px;
                width: max-content;

                .balanceText {
                    color: #68707B;
                    font-size: 19px;
                }

                .balanceContent {
                    color: #FFAA09;
                    font-size: 19px;
                }

                .refreshIconUp {
                    width: 2.2rem;
                    height: 2.2rem;
                    color: #f0c059;
                    cursor: pointer;
                    &.active {
                        -webkit-animation: spin .5s linear infinite;
                        animation: spin .5s linear infinite;
                    }
                }
            }
        }

        .transferInputContainer {
            .to{
                color: #f0c059;
                font-size: 20px;
            }
            width: 100%;
        }

        .buttonContainer {
            display: flex;
            align-items: center;
            justify-content: end;
            gap:1rem;
            .transferButton {
                background-color: #f0c059;
                color: #874404;
                font-weight: 600;
                height: 2.5rem;
                width: 10rem;
                line-height: normal;
            }
            .notice{
                color: #f05959;
                font-style: italic;
            }
        }
    }

    .mainContentContainer {
        display: flex;
        gap: 1.5rem;
        margin-bottom: 1.5rem;

        .sideButtonsContainer {
            .sideButtons {
                background-color: #1C1E23;
                width: 10rem;
                height: 4rem;
                color: #fff;
                font-weight: 600;
                letter-spacing: 2px;
                border-radius: 7px;

                &.active {
                    background-color: #f0c059;
                    color: #874404;
                }
            }
        }

        .rightContentConatainer {
            height: auto;
            width: 100%;
            border-radius: 7px;

            .searchBarContainer {
                background-color: #1C1E23;
                border-radius: 7px;
                padding: 1rem;
                display: flex;
                gap: 10px;
                margin-bottom: 1.5rem;

                .buttonBox {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .searchButton {
                        background-color: #f0c059;
                        color: #874404;
                        font-weight: 600;
                        height: 3.3rem;
                        width: 10rem;
                    }
                }

                .css-78me8v-MuiFormControl-root {
                    .css-nxo287-MuiInputBase-input-MuiOutlinedInput-input {
                        color: #fff;
                        font-weight: 600;
                    }

                    .MuiOutlinedInput-notchedOutline {
                        border-radius: 1rem;
                        border: thin solid #68707B;
                    }

                    .Mui-focused {
                        .MuiOutlinedInput-notchedOutline {
                            border: 2px solid #FFAA09;
                        }
                    }

                    .css-tzssek-MuiSvgIcon-root {
                        color: #68707B;
                    }
                }
            }

            .contentScroll {
                height: 60vh;
                overflow: auto;

                .contentBody {
                    display: grid;
                    grid-template-columns: auto auto auto auto auto;
                    gap: 1.5rem;

                    .contentBox {
                        display: flex;
                        flex-direction: column;
                        background-color: #1C1E23;
                        border-radius: 7px;

                        .headBox {
                            display: flex;
                            align-items: center;
                            padding: 7px 0 3px 0;

                            .gameImage {
                                width: 2rem;
                                margin-left: 7px;
                            }

                            .headTitle {
                                margin-left: 10px;
                                font-size: 19px;
                                font-weight: 500;
                                color: #fff;
                                padding: 3px 3px 3px 0;
                            }
                        }

                        .contentBody {
                            display: flex;
                            align-items: center;
                            justify-content: start;
                            padding: .5rem;
                            justify-content: space-between;

                            .labelBox {
                                .content {
                                    font-size: 25px;
                                    font-weight: 500;
                                    color: #FFAA09;
                                }
                            }

                            .iconBox {
                                .refreshIcon {
                                    width: 1.6rem;
                                    height: 1.6rem;
                                    color: #f0c059;
                                    cursor: pointer;
                                    &.active {
                                        -webkit-animation: spin .5s linear infinite;
                                        animation: spin .5s linear infinite;
                                    }
                                }
                                .css-78trlr-MuiButtonBase-root-MuiIconButton-root{
                                    padding: 0 !important;
                                }
                            }
                        }

                        .lineDiv {
                            border: thin solid #313843;
                        }
                    }
                }
            }

        }
    }
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.css-78trlr-MuiButtonBase-root-MuiIconButton-root{
    padding: 0 !important;
}