.gameContainers-tab {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.gameContainers-tab .gameSidebars {
  height: 100%;
  width: 15%;
  padding: 1rem;
  padding-top: 0;
}
.gameContainers-tab .gameSidebars .tabContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.gameContainers-tab .gameSidebars .tabContainer .tabs {
  background-color: #1C1E23;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.5rem;
  border-radius: 10px;
  border: 2px solid #9e9e9e;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}
.gameContainers-tab .gameSidebars .tabContainer .tabs .icon {
  width: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
  gap: 0.5rem;
}
.gameContainers-tab .gameSidebars .tabContainer .tabs .icon img {
  max-width: 3.5rem;
  width: 100%;
  height: 100%;
}
.gameContainers-tab .gameSidebars .tabContainer .tabs .icon span {
  color: #9e9e9e;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
}
.gameContainers-tab .gameSidebars .tabContainer .tabs:hover, .gameContainers-tab .gameSidebars .tabContainer .tabs.active {
  background-color: #f0c059;
  border: 2px solid #f0c059;
}
.gameContainers-tab .gameSidebars .tabContainer .tabs:hover span, .gameContainers-tab .gameSidebars .tabContainer .tabs.active span {
  color: #ffffff;
}
.gameContainers-tab .mainGames {
  background-color: #1C1E23;
  width: 100%;
  border-radius: 10px;
  overflow: auto;
  max-height: 60rem;
}
.gameContainers-tab .mainGames .gameContainer {
  padding: 1rem;
}
.gameContainers-tab .mainGames .gameContainer .gameTab {
  padding: 1rem;
  display: flex;
  gap: 2rem;
  -moz-column-gap: 2.3rem;
       column-gap: 2.3rem;
  flex-wrap: wrap;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub {
  position: relative;
  width: 11rem;
  background-image: url("/public/images/loading-bg.png");
  border-radius: 25px;
  cursor: pointer;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub .playBtn {
  position: absolute;
  bottom: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub .playBtn .icon {
  width: 5rem !important;
  height: 5rem !important;
  opacity: 0;
  transition: opacity 400ms ease;
  cursor: pointer;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub:hover .playBtn {
  position: absolute;
  bottom: 0;
  top: 0;
  background-color: rgba(5, 5, 5, 0.3490196078);
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub:hover .playBtn .icon {
  opacity: 1;
  transition: opacity 400ms ease;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub img {
  width: 11rem !important;
  height: 14rem !important;
}
.gameContainers-tab .mainGames .gameContainer .gameTab .gameTabContainerSub .game {
  width: 100%;
  height: 100%;
  border-radius: 25px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
  position: relative;
}

.frameContainer .frameControlers {
  background-color: #2b2d33;
  display: flex;
  padding: 0.5rem;
  border-radius: 1rem;
  justify-content: space-between;
}
.frameContainer .frameControlers .sizeClose {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.frameContainer .frameControlers .sizeClose .closeGame {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.frameContainer .frameControlers .sizeClose .closeGame img {
  height: 25%;
  width: 25%;
}
.frameContainer .frameControlers .sizeClose .fullScreen {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.frameContainer .frameControlers .sizeClose .fullScreen img {
  height: 25%;
  width: 25%;
}
.frameContainer .frameControlers .sizeClose div {
  color: #f0c059;
  font-size: 20px;
  cursor: pointer;
}
.frameContainer .frameControlers .frags {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.frameContainer .frameControlers .frags span {
  color: #f0c059;
  font-size: 1.5rem;
  cursor: pointer;
  text-transform: uppercase;
}
.frameContainer .frameControlers .frags img {
  max-width: 2rem;
  rotate: 270deg;
}
.frameContainer .frameControlers .frags .tail {
  color: #9e9e9e;
  cursor: default;
}
.frameContainer .frameGrme {
  background-color: #2b2d33;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  height: 45rem;
}
.frameContainer .frameGrme iframe {
  width: 100%;
  height: 100%;
}/*# sourceMappingURL=gamesTab.css.map */