.slider-container {
    img {
        height: 20vh;
        border-radius: 10px;
        width: 20vw;
        border: 2px solid rgb(65, 63, 63);
    }

ul {
    li {
        button::before {
            color: white;
            border-radius: 10px;
            margin: 0;
            padding: 0;
        }
        button.slick-active {
            color: red;
        }
    }
}
}
