.missionContainer {
  display: flex;
  margin: 1rem;
}
.missionContainer .fixedLeft {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.missionContainer .fixedLeft .leftSideButton {
  line-height: normal;
  background-color: #F0C059;
  color: #874404;
  font-weight: 600;
  width: 10rem;
}
.missionContainer .scrollRight {
  overflow: auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin: 0 1rem;
  padding: 0 1rem 0 0;
  height: 600px;
}
.missionContainer .scrollRight .banner {
  display: flex;
  align-items: center;
  background-color: #1C1E23;
  padding: 1rem;
  border-radius: 7px;
  gap: 5px;
}
.missionContainer .scrollRight .banner .phoneImageBox {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.missionContainer .scrollRight .banner .phoneImageBox .phoneImage {
  width: 3.5rem;
}
.missionContainer .scrollRight .banner .labelBox {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
}
.missionContainer .scrollRight .banner .labelBox .boxLabel {
  font-size: 20px;
  color: #fff;
}
.missionContainer .scrollRight .banner .labelBox .boxLabel .labelContent {
  color: #FFAA09;
}
.missionContainer .scrollRight .banner .labelBox .bonusLabel {
  color: #68707B;
}
.missionContainer .scrollRight .banner .buttonBox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18%;
}
.missionContainer .scrollRight .banner .buttonBox .downloadButton {
  background-color: #F0C059;
  color: #874404;
  font-weight: 600;
}
.missionContainer .scrollRight .contextBox {
  background-color: #1C1E23;
  padding: 1rem;
  border-radius: 7px;
  color: #fff;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.missionContainer .scrollRight .contextBox .missionLabels {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

