.feesMainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 1.5rem;

    .jurosContentContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: auto;
        background-color: #1C1E23;
        border-radius: 7px;

        .jurosMenuContainer {
            width: 100%;

            .jurosTabs {
                color: #fff;
                font-weight: 600;

                &:hover {
                    color: #e4b74f;
                }

                &.active {
                    color: #e4b74f;
                }
            }

            .css-ahj2mt-MuiTypography-root {
                padding: 0 !important;
            }

            .cardsContainer {
                display: flex;
                gap: 1.5rem;
                padding: 1rem;

                .cardBox1,
                .cardBox2,
                .cardBox3 {
                    height: 11rem;
                    color: #fff;
                    padding: 1rem;
                    width: 100%;
                    position: relative;
                    border-radius: 7px;

                    .labelBox {
                        .labelTitle {
                            font-size: 17px;
                            font-weight: 600;
                        }

                        .labelAmount {
                            font-size: 19px;
                            font-weight: 600;
                            position: absolute;
                            bottom: 4rem;
                        }

                        .labelEarningRate {
                            font-weight: 18px;
                            font-weight: 600;
                            position: absolute;
                            bottom: 2rem;
                        }
                    }

                    .coinBox {
                        position: absolute;
                        bottom: 0;
                        right: 1rem;
                    }
                }

                .cardBox1 {
                    background: linear-gradient(180deg, #79d873, #76d2ad);
                }

                .cardBox2 {
                    background: linear-gradient(180deg, #7adcf5, #54a9e2);
                }

                .cardBox3 {
                    background: linear-gradient(180deg, #64aff6, #4c6ef5);
                }

            }

            .graphContainer {
                display: flex;
                flex-direction: column;
                align-items: center;

                .graphTitle {
                    color: #fff;
                    font-size: 20px;
                }
            }

            .registrationMainContainer {
                width: 100%;
                padding: 1rem;

            }

            .regrasContainer {
                margin: 1rem;
                padding: 1rem;
                border: thin solid #313843;
                border-radius: 7px;
                color: #adb6b9;
                display: flex;
                flex-direction: column;
                height: auto;
                gap: 1rem;

                .balanceBonusBox {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .balanceTitle {
                        font-size: 18px;
                        font-weight: 600;
                        color: #fff;
                    }

                    .titleBox {
                        width: 85%;

                        .title {
                            font-size: 15px;
                            color: #fff;
                        }
                    }

                    .balanceBox {
                        border: thin solid #313843;
                        padding: 1rem;
                        border-radius: 7px;
                        position: relative;

                        .imageBox {
                            position: absolute;
                            bottom: -1rem;
                            right: 1rem;

                            .balanceImage {
                                width: 11rem;
                            }
                        }
                    }
                }

                .balanceInstructions {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .balanceTitle {
                        font-size: 18px;
                        font-weight: 600;
                        color: #fff;
                    }

                    .balanceTitleWithTable {
                        margin-top: 8px;
                    }

                    .boxContainer {
                        display: flex;
                        gap: 1rem;

                        .titleBox {
                            width: 85%;

                            .title {
                                font-size: 15px;
                                color: #fff;
                            }
                        }

                        .balanceBox1,
                        .balanceBox2,
                        .balanceBox3 {
                            border: thin solid #313843;
                            padding: 1rem;
                            border-radius: 7px;
                            position: relative;
                            width: 60%;
                            display: flex;
                            align-items: center;

                            .imageBox {
                                position: absolute;
                                bottom: 0;
                                right: 0;

                                .balanceImage {
                                    width: 5rem;
                                }
                            }
                        }

                        .balanceBox3 {
                            width: 100%;
                        }
                    }

                    .middleBoxConrtainer {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        gap: 2rem;
                        border: thin solid #313843;
                        padding: 2rem;
                        border-radius: 7px;
                        margin-top: 5px;
                        .firstBox{
                            width: 100%;
                            .firstP{
                                color: #fff;
                            }
                        }
                    }
                }

                .balanceCompliance {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .balanceTitle {
                        font-size: 18px;
                        font-weight: 600;
                        color: #fff;
                    }

                    .boxContainer {
                        display: flex;
                        gap: 1rem;

                        .titleBox {
                            width: 85%;

                            .title {
                                font-size: 15px;
                                color: #fff;
                            }
                        }

                        .balanceBox1,
                        .balanceBox2,
                        .balanceBox3 {
                            border: thin solid #313843;
                            padding: 1rem;
                            border-radius: 7px;
                            position: relative;
                            width: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            z-index: 1;

                            .spanBox {
                                position: absolute;
                                mix-blend-mode: overlay;

                                .spanNumber {
                                    font-size: 90px;
                                    font-weight: 600;
                                    color: rgb(28, 100, 245);
                                }
                            }

                            .titleBox {
                                .title {
                                    font-size: 17px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .bonusSearchButton {
        background-color: rgb(28, 100, 245);
        color: rgb(255, 255, 255);
        border-color: rgb(28, 100, 245);
        font-weight: 600;
    }
}