.rebateMainContainer {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;

  .rebateContainer {
    background-color: #1C1E23;
    border-radius: 7px;

    .css-heg063-MuiTabs-flexContainer {
      border-bottom: 2px solid #313843;
    }

    .MuiTabs-root {
      button {
        color: white !important;
        font-weight: 600;
      }

      button[aria-selected="true"] {
        color: #f0c059;
      }
    }

    .css-1776hvf {
      border: none;
    }

    .fishingMainContainer {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      padding: 1rem;

      .topBox {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .labelBox {
          display: flex;
          gap: 5px;
          justify-content: baseline;

          .labelTitle {
            color: #fff;
            font-style: 18px;
          }

          .labelContent {
            color: #f0c059;
            font-style: 18px;
            font-weight: 600;
          }
        }

        .buttonBox {
          .rebateButton {
            background-color: #f0c059;
            color: #fff;
            font-weight: 600;
            line-height: normal;
            width: 10rem;
          }
        }
      }

      .tableContainer {
        .cellContainer {
          display: flex;
          justify-content: space-between;

          .cellContent {
            color: #fff;
          }
        }

        .tdCellContainer {
          display: flex;
          flex-direction: column;

          .cellContainer {
            display: flex;
            justify-content: space-between;

            .labelBox {
              display: flex;
              align-items: center;
              justify-content: center;
              text-align: center;
              width: 5rem;
              word-break: break-all;
              .cellContent {
                text-align: center;
                color: #fff;
                font-size: 17px;
              }
            }
          }

          .labelContainer {
            background-color: #313843;
            padding: 5px;
            border-radius: 15px;
            .labelContent {
              color: #f0c059;
              font-size: 15px;
            }
          }
        }
        .platformImage{
          width: 4rem;
        }
      }
    }
  }
}