.supportSection {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    .supportHeader {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        // margin-bottom: 1.5rem;
        gap: 10px;
        border-radius: 7px;

        .titleWing {
            width: 5rem;

            &:nth-child(3) {
                rotate: 180deg;
            }
        }

        .supportHeaderTitle {
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }

    .supportMainContainer {
        background-color: #1C1E23;
        border-radius: 7px;
        margin-bottom: 1.5rem;

        .tablLink {
            &.active {
                button {
                    color: #f0c059;
                }
            }
        }

        .supportTabs {
            color: #fff;
            font-weight: 600;
        }

        .MuiTabs-root {
            button {
                color: white;
            }

            button[aria-selected="true"] {
                color: #f0c059;
            }
        }

        .noticeContainer {
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin: 1rem;
            margin-bottom: 0;
            gap: 1rem;
            background-color: #202329;
            padding: 1rem;
            border-radius: 7px;

            .noticeBox {
                display: flex;
                flex-direction: row;
                align-items: center;
                border-radius: 7px;
                background-color: #1C1E23;
                cursor: pointer;

                label {
                    cursor: pointer;
                }

                .iconBox {
                    width: 10%;
                    display: flex;
                    justify-content: center;

                    .noticeIcon {
                        color: green;
                        height: 5rem;
                        width: 3rem;
                    }

                    .noticeIconRead {
                        color: #f0c059;
                    }

                    .noticeIconSound {
                        color: #f0c059;
                    }
                }

                .noticeBoxContent {
                    display: flex;
                    flex-direction: column;
                    width: 100%;
                    gap: 5px;

                    .noticeContent {
                        color: green;
                        font-size: 18px;
                        font-weight: 500;
                    }

                    .noticeContentRead {
                        color: #f0c059;
                    }

                    .noticeDate {
                        color: #68707B;
                        font-size: 15px;
                    }

                    .noticeContentSound {
                        color: #fff;
                    }
                }

                .arrowBox {
                    display: flex;
                    align-items: center;
                    justify-content: end;
                    padding: 1rem;
                    gap: 10px;
                    width: 10%;

                    .arrowTitle {
                        color: #fff;
                        font-weight: 500;
                        font-size: 17px;
                    }

                    .arrowIcon {
                        height: 2rem;
                        width: 1.5rem;
                        color: #f0c059;
                    }
                }

                .noMsessage {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    margin: 1rem;

                    .noMessageImage {
                        width: 15rem;
                    }

                    .noMessageLabel {
                        font-size: 24px;
                        font-weight: 500;
                        color: #68707B;
                    }
                }
            }
        }

        .supportBox {
            display: flex;
            align-items: center;
            margin: 1rem;
            gap: 15px;

            .imageBox {
                width: 15%;
                display: flex;
                justify-content: center;

                .supportImage {
                    width: 5rem;
                }
            }

            .supportMessage {
                display: flex;
                flex-direction: column;
                gap: 5px;
                width: 100%;

                .supportTitleBox {
                    .supportTitle {
                        color: #fff;
                        font-size: 22px;
                        font-weight: 500;
                    }
                }

                .supportContentBox {
                    .supportContent {
                        color: #68707B;
                        font-size: 18px;
                    }
                }
            }

            .buttonBox {
                width: 15%;
                display: flex;
                justify-content: center;

                .supportButton {
                    border: 1px solid #f0c059;
                    background-color: #1C1E23;
                    color: #f0c059;
                    max-width: 6rem;
                    min-width: 6rem;
                }
            }
        }

        .supportBoxContent {
            .supportTele {
                margin: 1rem;
                display: grid;
                grid-template-columns: auto auto;
                gap: 1rem;

                .teleContent {
                    border: thin solid #313843;
                    border-radius: 7px;
                    padding: 1rem;
                    display: flex;
                    align-items: center;
                    gap: 15px;

                    .imageBox {
                        width: 10%;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .teleImage {
                            width: 3.5rem;
                        }
                    }

                    .contentBox {
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        line-height: normal;

                        .contentLabel {
                            font-size: 17px;
                            color: #fff;
                        }

                        .contentTitle {
                            color: #68707B;
                            font-weight: 600;
                        }
                    }

                    .boxButton {
                        .supportButton {
                            background-color: #f0c059;
                            color: #874404;
                            max-width: 6rem;
                            min-width: 6rem;
                            font-weight: 600;
                        }
                    }
                }
            }
        }

        .bonusContainer {
            .css-1776hvf {
                border: none;
            }

            .createBonusContainer {
                margin: 1rem;
                display: flex;
                flex-direction: column;
                gap: 10px;

                .textAreaLabelBox {
                    display: flex;
                    gap: 5px;

                    .textAreaLabel {
                        color: #fff;
                        font-size: 18px;
                    }

                    .textAreaLabelNote {
                        color: #68707B;
                        font-style: italic;
                    }
                }

                .textAreaBox {
                    width: 100%;

                    .bonusTextArea {
                        background-color: #1C1E23;
                        border: thin solid #313843;
                        border-radius: 7px;
                        color: #fff;
                        resize: vertical;
                        width: 100%;
                        padding: 1rem;
                        font-size: 17px;
                        font-weight: 600;
                        outline: none;
                        height: 250px;
                        min-height: 150px;
                        max-height: 300px;

                        &:focus {
                            border: 1px solid #f0c059 !important;
                        }
                    }

                    .textAreaCounter {
                        color: #68707B;
                        font-size: 18px;
                        width: 100%;
                        display: flex;
                        flex-direction: row-reverse;
                    }
                }

                .imagePickerContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .imagePickerLabelBox {
                        display: flex;
                        gap: 5px;

                        .imagePickerLabel {
                            color: #fff;
                            font-size: 18px;
                        }

                        .imagePickerLabelNote {
                            color: #68707B;
                            font-style: italic;
                        }
                    }

                    .imagePicker {
                        border: thin solid #313843;
                        border-radius: 7px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 7rem;
                        height: 7rem;
                        cursor: pointer;

                        .addIcon {
                            width: 3rem;
                            height: 5rem;
                            color: #313843;
                        }
                    }

                    .imageSizeLabel {
                        color: #68707B;
                        font-style: italic;
                        font-size: 15px;
                    }
                }

                .noticeImageContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .title {
                        color: #fff;
                        font-weight: 600;
                        font-size: 19px;
                    }

                    .titleBody {
                        color: #68707B;
                        font-size: 17px;
                    }
                }

                .buttonContainer {
                    .imagePickerButton {
                        background-color: #f0c059;
                        color: #874404;
                        font-weight: 600;
                    }
                }
            }

            .pendingBonus {
                margin: 1rem;
                display: flex;
                flex-direction: column;
                gap: 1rem;

                .pendingContainer {
                    display: flex;
                    flex-direction: row-reverse;
                    align-items: center;
                    gap: 15px;

                    .pendingTitle {
                        color: #68707B;
                        font-size: 19px;
                    }

                    .pendingTitleContent {
                        color: #f0c059;
                        font-weight: 600;
                        font-size: 19px;
                    }

                    .pendingButton {
                        color: #874404;
                        background-color: #f0c059;
                        font-weight: 600;
                    }
                }

                .pendingBonusContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;
                    background-color: #202329;
                    height: auto;
                    border-radius: 7px;
                    padding: 1rem;

                    .noticeBox {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        border-radius: 7px;
                        background-color: #1C1E23;
                        cursor: pointer;

                        label {
                            cursor: pointer;
                        }

                        .iconBox {
                            width: 10%;
                            display: flex;
                            justify-content: center;

                            .noticeIcon {
                                color: green;
                                height: 5rem;
                                width: 3rem;
                            }

                            .noticeIconRead {
                                color: #f0c059;
                            }

                            .noticeIconSound {
                                color: #f0c059;
                            }
                        }

                        .noticeBoxContent {
                            display: flex;
                            flex-direction: column;
                            width: 100%;
                            gap: 5px;

                            .noticeContent {
                                color: green;
                                font-size: 18px;
                                font-weight: 500;
                            }

                            .noticeContentRead {
                                color: #f0c059;
                            }

                            .noticeDate {
                                color: #68707B;
                                font-size: 15px;
                            }

                            .noticeContentSound {
                                color: #fff;
                            }
                        }

                        .arrowBox {
                            display: flex;
                            align-items: center;
                            justify-content: end;
                            padding: 1rem;
                            gap: 10px;
                            width: 10%;

                            .arrowTitle {
                                color: #fff;
                                font-weight: 500;
                                font-size: 17px;
                            }

                            .arrowIcon {
                                height: 2rem;
                                width: 1.5rem;
                                color: #f0c059;
                            }
                        }

                        .noMsessage {
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            width: 100%;
                            margin: 1rem;

                            .noMessageImage {
                                width: 15rem;
                            }

                            .noMessageLabel {
                                font-size: 24px;
                                font-weight: 500;
                                color: #68707B;
                            }
                        }
                    }

                    .viewFeedbackButton {
                        line-height: normal;
                        font-size: 11px;
                        border: thin solid;
                        border-color: #f0c059;
                        color: #f0c059;
                    }
                }
            }
        }
    }
}

.modalButton {
    color: #f0c059 !important;
    border: thin solid !important;
    border-color: #f0c059 !important;
}

.css-nm1wuk-MuiTypography-root-MuiDialogTitle-root {
    background-color: #17191d;
    color: #f0c059;
}

.css-153eyq5-MuiModal-root-MuiDialog-root .MuiDialogContent-root {
    background-color: #1C1E23;
    color: #fff;
}

html {
    scroll-padding-top: 120px !important;
}

.replyContainer {
    display: flex;
    flex-direction: column;

    .topBoxReply {
        margin-bottom: 1rem;
        .contentBox {
            display: flex;
            gap: 5px;

            .contentLabel {
                color: #fff;
            }

            .content {
                color: #f0c059;
            }
        }
    }

    .textAreaReply {
        margin-top: 1rem;
    }

    .replyBox {
        display: flex;
        flex-direction: row;
        gap: 10px;

        .yourBox {
            display: flex;
            align-items: center;
            // gap: 5px;
            .yourLabel {
                color: #fff;
                &.active {
                    color: red;
                }
            }
            .yourReply {
                color: #f0c059;
                font-style: italic;
            }
            .yourReplyTime {
                color: #f0c059;
                font-style: italic;
            }
            .openClose{
                color: #f0c059;
                &.active {
                    color: red;
                }
            }
        }
    }
}