.profileMainContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    .profileHeader{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4rem;
        background-color: #1C1E23;
        // margin-bottom: 1.5rem;
        gap: 10px;
        border-radius: 7px;
        .titleWing{
            width: 5rem;
            &:nth-child(3){
                rotate: 180deg;
            }
        }
        .profileHeaderTitle{
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
    }
    .profileContainer{
        background-color: #1C1E23;
        padding: 2rem 0 5rem 0;
        margin-bottom: 1.5rem;
        border-radius: 7px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
        .profileBox{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            .profileImageBox{
                width: 40rem;
                display: flex;
                align-items: center;
                justify-content: start;
                gap: 5rem;
                .profileImage{
                    width: 5.5rem;
                }
                .configButton{
                    background-color: #F0C059;
                    color: #874404;
                    font-weight: 600;
                }
            }
        }
        .inputContainer{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .MuiStack-root{
                .MuiOutlinedInput-notchedOutline{
                    border: thin solid #313843;
                }
                .inputIcon{
                    color: #F0C059;
                }
                .css-1o9s3wi-MuiInputBase-input-MuiOutlinedInput-input{
                    color: #fff;
                }
                .css-1o9s3wi-MuiInputBase-input-MuiOutlinedInput-input.Mui-disabled{
                    -webkit-text-fill-color: #fff;
                }
                .css-152mnda-MuiInputBase-input-MuiOutlinedInput-input.Mui-disabled{
                    -webkit-text-fill-color: #b8b8b8;
                }
                .whatsAppIcon{
                    color: green;
                }
                .teleIcon{
                    color: #fff;
                }
                .Mui-focused{
                    border: thin solid #F0C059;
                    transition: 0.5s;
                }
            }
            .configButton{
                background-color: #F0C059;
                color: #874404;
                font-weight: 600;
            }
        }
        .profileFooter{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 2rem;
            .labelBox{
                width: 40rem;
                .footerLabel{
                    color: #fff;
                    font-size: 20px;
                }
            }
            .inputBox{
                width: 40rem;

            }
            .configButton{
                background-color: #F0C059;
                color: #874404;
                font-weight: 600;
                width: 10rem;
                height: 3rem;
            }
            .returnButton{
                background-color: transparent;
                border: 1px solid #F0C059;
                color: #F0C059;
            }
            .MuiStack-root{
                .MuiOutlinedInput-notchedOutline{
                    border: thin solid #313843;
                }
                .css-1t8l2tu-MuiInputBase-input-MuiOutlinedInput-input{
                    color: #fff;
                }
                .css-1o9s3wi-MuiInputBase-input-MuiOutlinedInput-input.Mui-disabled{
                    -webkit-text-fill-color: #fff;
                }
                .css-152mnda-MuiInputBase-input-MuiOutlinedInput-input.Mui-disabled{
                    -webkit-text-fill-color: #b8b8b8;
                }
                .Mui-focused{
                    border: thin solid #F0C059;
                    transition: 0.5s;
                }
                .css-11u53oe-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input.MuiSelect-select{
                    color: #fff;
                }
                .css-bpeome-MuiSvgIcon-root-MuiSelect-icon,
                .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon{
                    color: #fff;
                }
            }
        }
    }
}